import {
  Component,
  ViewChild,
  ElementRef,
  Input,
  OnInit,
  Output,
  EventEmitter
} from '@angular/core';
import { some, get, map } from 'lodash-es';
import { gettext } from '../i18n/gettext';

@Component({
  selector: 'c8y-drop-area',
  templateUrl: './drop-area.component.html'
})
export class DropAreaComponent implements OnInit {
  @Input() title = gettext('Upload file');
  @Input() message = gettext('Drop file here');
  @Input() icon = 'plus-square';
  @Input() loadingMessage = gettext('Uploadingâ€¦');
  /** Affects displaying both the drop zone and the list of dropped files. */
  @Input() alwaysShow = false;
  @Input() clickToOpen = true;
  @Input() loading = false;
  @Input() progress = -1; // -1 = spinner
  @Output() dropped: EventEmitter<DroppedFile[]> = new EventEmitter();
  @Input() maxAllowedFiles = Infinity;
  @Input() files: FileList;
  isOver: boolean = false;
  errors: boolean = false;
  errorMessage: string;
  filesNameString: string;

  @ViewChild('area', { static: true }) area: ElementRef;
  @ViewChild('zone', { static: false }) zone: ElementRef;
  @ViewChild('picker', { static: false }) picker: ElementRef;

  ngOnInit() {
    this.alwaysShow = this.alwaysShow || this.area.nativeElement.children.length === 0;
    if (this.files && this.files.length > 0) {
      this.onFilesSelected(this.files);
    }
  }

  toggle($event?) {
    this.zone.nativeElement.style.height = this.area.nativeElement.offsetHeight + 'px';
    this.onOver();
  }

  showPicker($event?) {
    this.preventDefault($event);
    this.picker.nativeElement.value = '';
    this.picker.nativeElement.click();
  }

  onOver() {
    if (!this.isOver) {
      this.isOver = true;
      document.addEventListener('dragover', this.preventDefault);
      document.addEventListener('drop', this.preventDefault);
    }
  }

  onPick($event) {
    this.errors = false;
    this.preventDefault($event);
    this.onFilesSelected($event.target.files);
  }

  onDrop($event) {
    this.preventDefault($event);
    this.onFilesSelected($event.dataTransfer.files);
    this.stopDragging();
  }

  onFilesSelected(files: FileList) {
    this.files = files;
    this.filesNameString = this.getFilesNamesAsString(files);
    this.errors = false;
    if (!this.isFilesArrayEmpty() && !this.isTooManyFiles()) {
      if (this.hasEmptyFiles()) {
        this.errors = true;
        this.errorMessage = gettext('File must not be empty, select another one.');
      } else {
        this.dropped.emit(this.compose(files));
      }
    } else {
      this.errors = true;
      this.errorMessage = gettext('Too many files selected.');
    }
  }

  getFilesNamesAsString(files: FileList): string {
    return map(files, ({ name }) => name).join(', ');
  }

  isFilesArrayEmpty() {
    return get(this, 'files.length', 0) === 0;
  }

  isTooManyFiles() {
    return get(this, 'files.length', 0) > this.maxAllowedFiles;
  }

  hasEmptyFiles() {
    let result = true;
    if (!this.isFilesArrayEmpty()) {
      result = this.isAnyFileEmpty();
    }
    return result;
  }

  isAnyFileEmpty(): boolean {
    return some(Array.from(this.files), ['size', 0]);
  }

  shouldShowFilesList() {
    return (
      this.alwaysShow &&
      !this.isFilesArrayEmpty() &&
      !this.hasEmptyFiles() &&
      !this.isTooManyFiles()
    );
  }

  stopDragging() {
    document.removeEventListener('dragover', this.preventDefault);
    document.removeEventListener('drop', this.preventDefault);
    this.isOver = false;
  }

  onDelete() {
    delete this.files;
    delete this.filesNameString;
    this.clearErrors();
    this.dropped.emit(undefined);
  }

  clearErrors() {
    delete this.errorMessage;
    this.errors = false;
  }

  private preventDefault($event?) {
    if ($event) {
      $event.preventDefault();
    }
  }

  private compose(files: FileList) {
    return Array.from(files).map(file => ({
      file,
      readAsJson: async () => JSON.parse(await this.read(file, ReadAsType.TEXT)),
      readAsText: async () => this.read(file, ReadAsType.TEXT),
      readAsArrayBuffer: async () => this.read(file, ReadAsType.ARRAY_BUFFER),
      readAsBinaryString: async () => this.read(file, ReadAsType.BINARY_STRING),
      readAsDataURL: async () => this.read(file, ReadAsType.DATA_URL)
    }));
  }

  private async read(file, type: ReadAsType): Promise<string> {
    return new Promise<string>((resolve, reject) => {
      const reader = new FileReader();
      switch (type) {
        case ReadAsType.TEXT: {
          reader.readAsText(file);
          break;
        }
        case ReadAsType.ARRAY_BUFFER: {
          reader.readAsArrayBuffer(file);
          break;
        }
        case ReadAsType.BINARY_STRING: {
          reader.readAsBinaryString(file);
          break;
        }
        case ReadAsType.DATA_URL: {
          reader.readAsDataURL(file);
          break;
        }
      }
      reader.onload = () => this.onLoad(reader, resolve, reject);
    });
  }

  private onLoad(reader, resolve, reject) {
    if (reader.readyState !== 2) {
      return;
    }
    if (reader.error) {
      reject(reader.error);
    }
    resolve(reader.result);
  }
}

export interface DroppedFile {
  file: File;
  readAsText();
  readAsArrayBuffer();
  readAsBinaryString();
  readAsDataURL();
  readAsJson();
}

enum ReadAsType {
  TEXT,
  DATA_URL,
  ARRAY_BUFFER,
  BINARY_STRING
}
