import { Injectable, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { filter, mergeMap, take } from 'rxjs/operators';
import { ExtensionPoint, fromFactories } from '../common/extension-hooks';
import { StateService } from '../common/state-service.abstract';
import {
  DynamicComponentDefinition,
  HOOK_COMPONENT,
  HOOK_COMPONENTS
} from './dynamic-component.model';

declare const __MODE__: string;

@Injectable({
  providedIn: 'root'
})
export class DynamicComponentService extends StateService
  implements ExtensionPoint<DynamicComponentDefinition> {
  items$: Observable<DynamicComponentDefinition[]>;
  readonly refreshTrigger = new Subject();
  readonly state$ = new BehaviorSubject<Set<DynamicComponentDefinition>>(
    new Set<DynamicComponentDefinition>()
  );

  constructor(private injector: Injector, router: Router) {
    super();

    this.items$ = fromFactories(
      [() => this.injector.get(HOOK_COMPONENTS, []), () => this.getComponentsViaDeprecatedHook()],
      router,
      false
    );
    this.items$.subscribe(cmps => {
      cmps.forEach(cmp => {
        this.state.add(cmp);
      });
      this.emitNewState();
    });
  }

  getComponentsViaDeprecatedHook() {
    const componentsViaDeprecatedHook: DynamicComponentDefinition[] = this.injector.get(
      HOOK_COMPONENT,
      []
    ) as DynamicComponentDefinition[];
    if (
      componentsViaDeprecatedHook &&
      componentsViaDeprecatedHook.length &&
      __MODE__ !== 'production'
    ) {
      console.warn('HOOK_COMPONENT is now deprecated. Use HOOK_COMPONENTS instead.');
    }

    return componentsViaDeprecatedHook;
  }

  refresh() {
    this.refreshTrigger.next(1);
  }

  get state() {
    return this.state$.value;
  }

  /**
   * Returns a component by it's id as an observable.
   * @param id The id of the component.
   */
  getById$(id) {
    return this.state$.pipe(
      filter((cmps: Set<DynamicComponentDefinition>) => cmps.size > 0),
      mergeMap((cmps: Set<DynamicComponentDefinition>) =>
        of(Array.from(cmps).find(cmp => cmp.id === id))
      ),
      take(1)
    );
  }

  add(component: DynamicComponentDefinition) {
    this.state.add(component);
    this.emitNewState();
  }

  remove(component: DynamicComponentDefinition) {
    this.state.delete(component);
    this.emitNewState();
  }
}
