import { Component, Input } from '@angular/core';
import { NavigatorNode } from './navigator-node';

@Component({
  selector: 'c8y-navigator-icon',
  template: `<i class="icon" [c8yIcon]="icon"></i>`
})
export class NavigatorIconComponent {
  @Input() node: NavigatorNode;
  get icon() {
    const DEFAULT_ICON = 'cog';
    return this.node.icon || DEFAULT_ICON;
  }

  get iconClass() {
    return `fa-${this.icon}`;
  }
}
