import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DoCheck,
  Injector,
  Input,
  OnChanges,
  TemplateRef,
  Type
} from '@angular/core';
import { isEqual } from 'lodash-es';

import { FilteringFormRendererContext, FilteringFormRendererSpec } from './filtering-form-renderer';

@Component({
  selector: 'c8y-filtering-form-renderer',
  templateUrl: './filtering-form-renderer.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FilteringFormRendererComponent implements OnChanges {
  spec: FilteringFormRendererSpec;
  @Input('spec')
  set _spec(spec: FilteringFormRendererSpec) {
    this.spec = spec;
  }

  rendererType: 'TEMPLATE' | 'COMPONENT';
  rendererInjector: Injector;
  defaultRendererFilterText = '';

  constructor(private injector: Injector, private cd: ChangeDetectorRef) {}

  ngOnChanges() {
    const { renderer, context } = this.spec;

    if (renderer instanceof TemplateRef) {
      this.rendererType = 'TEMPLATE';
    } else if (renderer instanceof Type) {
      this.rendererType = 'COMPONENT';

      this.rendererInjector = Injector.create({
        providers: [{ provide: FilteringFormRendererContext, useValue: this.spec.context }],
        parent: this.injector
      });
    }

    const { filterPredicate } = context.property;
    if (typeof filterPredicate === 'string') {
      this.defaultRendererFilterText = filterPredicate;
    }
  }
}
