import { Component, HostBinding, Input, AfterContentInit } from '@angular/core';
import { filter, sortBy } from 'lodash-es';
import { ActionBarItem } from './action-bar.model';

@Component({
  selector: 'c8y-action-bar',
  templateUrl: './action-bar.component.html',
  host: { class: 'c8y-ui-action-bar' }
})
export class ActionBarComponent {
  @Input() @HostBinding('class.navigator-open') navigatorOpen = false;
  @Input() @HostBinding('class.has-tabs') hasTabs = false;
  @Input() @HostBinding('class.horizontal-tabs') isTabsHorizontal = false;
  isPageToolbarExpanded = false;
  @HostBinding('class.c8y-ui-action-bar') uiActionBar = true;
  @Input() items: ActionBarItem[] = [];

  @HostBinding() get hidden() {
    return !(this.items && this.items.length);
  }

  getSortedItems(placement: 'left' | 'right' | 'more') {
    return sortBy(filter(this.items, { placement }), this.byPriority);
  }

  private byPriority(item) {
    return -item.priority;
  }
}
