import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DoCheck,
  Injector,
  Input,
  OnChanges,
  TemplateRef,
  Type
} from '@angular/core';

import { CellRendererContext, CellRendererSpec } from './cell-renderer';

@Component({
  selector: 'c8y-cell-renderer',
  templateUrl: './cell-renderer.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CellRendererComponent implements OnChanges, DoCheck {
  spec: CellRendererSpec;
  @Input('spec')
  set _spec(spec: CellRendererSpec) {
    this.spec = spec;
  }
  prevSpec: CellRendererSpec;

  rendererType: 'TEMPLATE' | 'COMPONENT';
  rendererInjector: Injector;

  constructor(private injector: Injector, private cd: ChangeDetectorRef) {}

  ngOnChanges() {
    if (!this.prevSpec) {
      this.prevSpec = this.spec;
    }

    const { renderer } = this.spec;

    if (renderer instanceof TemplateRef) {
      this.rendererType = 'TEMPLATE';
    } else if (renderer instanceof Type) {
      this.rendererType = 'COMPONENT';

      this.rendererInjector = Injector.create({
        providers: [{ provide: CellRendererContext, useValue: this.spec.context }],
        parent: this.injector
      });
    }
  }

  ngDoCheck() {
    if (!this.spec.context.value === this.prevSpec.context.value) {
      this.prevSpec = this.spec;
      this.cd.markForCheck();
    }
  }
}
