import { Component, OnInit } from '@angular/core';
import { OptionsService } from '../../common/options.service';

@Component({
  selector: 'c8y-cookie-banner',
  templateUrl: './cookie-banner.template.html'
})
export class CookieBannerComponent implements OnInit {
  readonly storageKey: string = 'acceptCookieNotice';
  readonly acceptValue: string = '1';
  showBanner: boolean;
  settings: { cookieBannerTitle?: string; cookieBannerText: string; policyUrl?: string };

  constructor(private options: OptionsService) {}

  ngOnInit() {
    this.settings = this.options.get('cookieBanner');
    if (this.settings && this.settings.cookieBannerText && !localStorage.getItem(this.storageKey)) {
      this.showBanner = true;
    }
  }

  acceptCookies() {
    localStorage.setItem(this.storageKey, this.acceptValue);
    this.showBanner = false;
  }
}
