import { Component, Input } from '@angular/core';
import { IOperation } from '@c8y/client';
import { operationStatusClasses, operationStatusIcons } from './status.model';

/**
 * Single operation component displays a single operation status, description and creationTime.
 *
 * Example:
 *
 * ```html
 * <c8y-single-operation [operation]="operation"></c8y-single-operation>
 * ```
 */
@Component({
  selector: 'c8y-single-operation',
  templateUrl: './single-operation.component.html'
})
export class SingleOperationComponent {
  @Input() operation: IOperation;
  statusIcons: object;
  statusClasses: object;

  ngOnInit() {
    this.statusIcons = operationStatusIcons;
    this.statusClasses = operationStatusClasses;
  }
}
