import { Component, Input} from '@angular/core';
import { gettext } from '../i18n/gettext';

@Component({
  selector: 'c8y-search-result-empty',
  templateUrl: './search-result-empty.component.html'
})
export class SearchResultEmptyComponent {
  private readonly NO_RESULT_FOUND = gettext('No results found.');
  private msg: string = this.NO_RESULT_FOUND;

  @Input('message')
  set _message(msg: string) {
    if (msg && msg.length > 0) {
      this.msg = msg;
    } else {
      this.msg = this.NO_RESULT_FOUND;
    }
  }

  get message() {
    return this.msg;
  }
}
