import { Component, Input, ViewChild } from '@angular/core';
import { Action } from './action.model';
import { ActionService } from './action.service';

@Component({
  selector: 'c8y-action',
  templateUrl: './action.component.html'
})
export class ActionComponent implements Action {
  @Input() disabled: boolean = false;
  @Input() label: string;
  @Input() priority: number = 0;
  @Input() icon: string;
  @ViewChild('template', {static: false}) template: any;
  action: () => void;
  private viewInitTimeout;

  constructor(private actionService: ActionService) {}

  ngAfterViewInit() {
    this.viewInitTimeout = setTimeout(() =>
      this.actionService.add(this as Action)
      );
  }

  ngOnDestroy() {
    clearTimeout(this.viewInitTimeout);
    this.actionService.remove(this as Action);
  }
}
