import { coerceNumberProperty } from '@angular/cdk/coercion';
import { IIdentified, Realtime } from '@c8y/client';
import { Observable, Subject } from 'rxjs';
import { filter, finalize, map } from 'rxjs/operators';
import { RealtimeMessage } from './realtime.model';

/**
 * A wrapper class for handling realtime notifications in RxJS fashion.
 */
export abstract class RealtimeService<T> {
  /**
   * A flag displaying if realtime notifications are currently active.
   */
  get active(): boolean {
    return this.isActive;
  }

  private subjects$: Map<string, RealtimeSubject<T>> = new Map<string, RealtimeSubject<T>>();
  private isActive: boolean = true;

  constructor(protected realtime: Realtime) {}

  /**
   * Get an Observable of all realtime notifications.
   *
   * @param {string | number | IIdentified} entityOrId Entity object or id
   *
   * @returns An [[Observable]] of notifications wrapped as [[RealtimeMessage]]
   */
  onAll$(entityOrId?: string | number | IIdentified): Observable<RealtimeMessage<T>> {
    const subject$: RealtimeSubject<T> = this.getSubjectForChannel(entityOrId);

    return subject$.pipe(
      finalize(() => {
        if (subject$.observers.length === 1 && subject$.observers[0].closed) {
          subject$.stop();
          this.subjects$.delete(subject$.channel);
        }
      })
    );
  }

  /**
   * Subscribes again all realtime channels with active observers.
   */
  start() {
    if (!this.isActive) {
      this.subjects$.forEach(subject$ => {
        subject$.start();
      });
      this.isActive = true;
    }
  }

  /**
   * Stops realtime notifications and unsubscribes all realtime channels.
   */
  stop() {
    if (this.isActive) {
      this.subjects$.forEach(subject$ => {
        subject$.stop();
      });

      this.isActive = false;
    }
  }

  /**
   * Get an Observable of all CREATE realtime notifications.
   *
   * @returns An [[Observable]] of newly created entity objects.
   */
  onCreate$(): Observable<T> {
    return this.onAll$().pipe(
      filter(msg => msg.realtimeAction === 'CREATE'),
      map(msg => msg.data as T)
    );
  }

  /**
   * Get an Observable of all UPDATE realtime notifications.
   *
   * @param {string | number | IIdentified} entityOrId Entity object or id
   *
   * @returns An [[Observable]] of updated entity objects.
   */
  onUpdate$(entityOrId?: string | number | IIdentified): Observable<T> {
    return this.onAll$(entityOrId).pipe(
      filter(msg => msg.realtimeAction === 'UPDATE'),
      map(msg => msg.data as T)
    );
  }

  /**
   * Get an Observable of all DELETE realtime notifications.
   *
   * @param {string | number | IIdentified} entityOrId Entity object or id
   *
   * @returns An [[Observable]] of deleted entity objects.
   */
  onDelete$(entityOrId?: string | number | IIdentified): Observable<number> {
    return this.onAll$(entityOrId).pipe(
      filter(msg => msg.realtimeAction === 'DELETE'),
      map(msg => coerceNumberProperty(msg.data))
    );
  }

  protected getIdString(reference: number | string | IIdentified): string {
    let id: string | number;
    if (typeof reference === 'object') {
      id = reference.id;
    } else {
      id = reference;
    }
    return String(id);
  }

  protected getChannel(entityOrId?: string | number | IIdentified) {
    return entityOrId ? this.channel().replace('*', this.getIdString(entityOrId)) : this.channel();
  }

  protected abstract channel(): string;

  private getSubjectForChannel(entityOrId?: string | number | IIdentified): RealtimeSubject<T> {
    const channel: string = this.getChannel(entityOrId);
    let subject$: RealtimeSubject<T>;
    if (this.subjects$.has(channel)) {
      subject$ = this.subjects$.get(channel);
    } else {
      subject$ = new RealtimeSubject(channel, this.realtime);
      this.subjects$.set(channel, subject$);
    }

    return subject$;
  }
}

// tslint:disable-next-line: max-classes-per-file
class RealtimeSubject<T> extends Subject<RealtimeMessage<T>> {
  realtimeSubscription: object;
  get channel(): string {
    return this.realtimeChannel;
  }

  constructor(private realtimeChannel: string, private realtime: Realtime) {
    super();
    this.start();
  }

  start() {
    if (!this.realtimeSubscription) {
      this.realtimeSubscription = this.realtime.subscribe(this.realtimeChannel, msg => {
        const data: RealtimeMessage<T> = {
          channel: msg.channel,
          data: msg.data.data,
          id: msg.id,
          realtimeAction: msg.data.realtimeAction
        };
        this.next(data);
      });
    }
  }

  stop() {
    if (this.realtimeSubscription) {
      this.realtime.unsubscribe(this.realtimeSubscription);
      this.realtimeSubscription = null;
    }
  }
}
