import { Directive, EventEmitter, Output, OnInit, OnDestroy } from '@angular/core';
import { NgControl, AbstractControl } from '@angular/forms';

import { debounceTime, distinctUntilChanged, tap } from 'rxjs/operators';
import { Subscription } from 'rxjs';
import { gettext } from '../i18n/public-api';

@Directive({
  selector: 'textarea[prettyValidJson]'
})
export class JsonValidationPrettifierDirective implements OnInit, OnDestroy {
  @Output() invalidJSON: EventEmitter<string> = new EventEmitter();
  private abstractCtrl: AbstractControl;
  private message: string = gettext('Must be a valid JSON object.');
  private debounceTimeInMs: number = 1000;
  private subscription: Subscription;

  constructor(ngCtrl: NgControl) {
    this.abstractCtrl = ngCtrl.control;
  }

  ngOnInit() {
    this.subscription = this.abstractCtrl.valueChanges
      .pipe(
        tap(() => this.clearValidationMessage()),
        debounceTime(this.debounceTimeInMs),
        distinctUntilChanged()
      )
      .subscribe(value => {
        this.validateInputAndPrettify(value);
      });
  }

  ngOnDestroy() {
    if (this.subscription && !this.subscription.closed) {
      this.subscription.unsubscribe();
    }
  }

  validateInputAndPrettify(value) {
    if (value) {
      try {
        const validJsonObject = JSON.parse(value);
        const pretty = JSON.stringify(validJsonObject, undefined, 2);
        this.abstractCtrl.setValue(pretty);
      } catch (ex) {
        this.setErrorAndValidationMessage();
      }
    }
  }

  private setErrorAndValidationMessage() {
    this.abstractCtrl.setErrors({ invalidBodyTemplate: true });
    this.invalidJSON.emit(this.message);
  }

  private clearValidationMessage() {
    this.invalidJSON.emit(undefined);
  }
}
