import { Component, OnInit } from '@angular/core';
import { ITotp, UserService } from '@c8y/client';
import { AlertService } from '../alert/alert.service';

@Component({
  selector: 'c8y-totp-setup',
  templateUrl: './totp-setup.component.html'
})

export class TotpSetupComponent implements OnInit {
  totpSecret: ITotp;

  get qrCodeImage() {
    return this.totpSecret ? this.totpSecret.secretQrUrl : '';
  }

  get secret() {
    return this.totpSecret ? this.totpSecret.rawSecret : '';
  }

  constructor(private user: UserService, private alert: AlertService) { }

  async ngOnInit() {
    try {
      const { data } = await this.user.generateTotpSecret();
      this.totpSecret = data;
    } catch (ex) {
      this.alert.addServerFailure(ex);
    }
  }
}
