import { Component, Input, SimpleChanges } from '@angular/core';
import { NavigatorNode } from './navigator-node';
import { HeaderService } from '../header/public-api';

@Component({
  selector: 'c8y-navigator-outlet, c8y-ui-navigator',
  templateUrl: './navigator-outlet.template.html',
})
export class NavigatorOutletComponent {
  @Input() nodes: NavigatorNode[];
  @Input() open = true;

  constructor(private headerService: HeaderService) {}

  /**
   * Closes the navigator on click if the window is small.
   */
  collapseNavigator(from: 'icon' | 'expander' | 'link') {
    if (from === 'link' && !this.headerService.largeWidth) {
      this.headerService.closeNavigator();
    }
  }

  trackByPathOrLabel(index, node) {
    return node.path ? node.path : node.label;
  }
}
