import { Component, Input, ViewChild, OnDestroy } from '@angular/core';
import { PopoverDirective } from 'ngx-bootstrap/popover';
import { StatusType } from '../common/status.model';
import { gettext } from '../i18n/gettext';

export interface PopoverConfirmButtons {
  /**
   * The label of the button
   */
  label: string;
  /**
   * Which action to perform.
   */
  action: () => Promise<any>;
  /**
   * The status class attached to the button.
   */
  status?: StatusType | 'primary';
}

/**
 * A component to show a popover right next to the
 * user action. Add this component where the confirm modal
 * should be shown and toggle isOpen to display it. You
 * can use `await isShow()` to wait for a result on the
 * click action.
 */
@Component({
  selector: 'c8y-popover-confirm',
  templateUrl: './popover-confirm.component.html'
})
export class PopoverConfirmComponent {
  @ViewChild(PopoverDirective, {static: false}) popover: PopoverDirective;
  @Input() buttons: PopoverConfirmButtons[] = [{ label: gettext('cancel'), action: () => Promise.reject()}];
  @Input() message: string;
  @Input() title: string;
  @Input() isOpen = false;
  @Input() containerClass = '';
  @Input() placement = 'right';
  @Input() outsideClick = false;

  click: (action: () => Promise<any>) => void;

  /**
   * Show the popover. Wait until an action is performed on it.
   */
  async show(buttons?: PopoverConfirmButtons[]) {
    this.buttons = buttons || this.buttons;
    this.isOpen = true;
    let result;
    try {
      result = await new Promise((resolve, reject) => {
        return this.click = (action: () => Promise<any>) => action().then(resolve).catch(reject);
      });
    } catch (ex) {
      throw(ex);
    } finally {
      this.isOpen = false;
    }
    return result;
  }

  buttonStatus(status) {
    if (status) {
      return `btn-${status}`;
    }
    return 'btn-link btn-clean';
  }

  handleOutsideClick() {
    this.click(() => Promise.reject());
  }
}
