import { AfterViewInit, Component } from '@angular/core';
import { Observable } from 'rxjs';
import { ActionService } from '../../action/action.service';
import { BreadcrumbService } from '../../breadcrumb/breadcrumb.service';
import { SearchService } from '../../search/search.service';
import { AppStateService } from '../../common/ui-state.service';
import { UserMenuService } from '../../user/user-menu.service';
import { HeaderService } from '../header.service';
import { DocsService } from '../../docs/docs.service';

@Component({
  selector: 'c8y-header-bar',
  templateUrl: './header-bar.component.html'
})
export class HeaderBarComponent implements AfterViewInit {
  canToggle$: Observable<boolean> = this.headerService.hasItemsInNavigator$;
  appState$: Observable<any> = this.appState.state$;
  drawerOpen = false;
  headerOpen = false;

  constructor(
    public headerService: HeaderService,
    public actionService: ActionService,
    public breadcrumbService: BreadcrumbService,
    public searchService: SearchService,
    private appState: AppStateService,
    public userMenuService: UserMenuService,
    public docs: DocsService
  ) {}

  ngAfterViewInit() {
    this.headerService.verifyIfNavOpen();
  }

  toggle() {
    this.headerOpen = !this.headerOpen;
  }
}
