import { Component, EventEmitter, Input, Output, SimpleChanges } from '@angular/core';
import { DashboardSettings, Widget, WidgetChange, DashboardChange } from './dashboard.model';
import { DashboardComponent } from './dashboard.component';

@Component({
  selector: 'c8y-widgets-dashboard',
  templateUrl: './widgets-dashboard.component.html',
  host: {
    style: `
      display: block;
    `,
    class: 'dashboard c8y-grid-dashboard'
  }
})
export class WidgetsDashboardComponent {
  @Input()
  widgets: Widget[];

  @Input()
  context: any;

  @Input()
  contextDashboard: any;

  @Input('settings')
  set _settings(settings: Partial<DashboardSettings>) {
    this.settings = { ...this.settings, ...settings };
  }
  settings: DashboardSettings = {
    isFrozen: false,
    isDisabled: false,
    widgetMargin: 12,
    translateWidgetTitle: false,
    defaultHeight: 4,
    defaultWidth: 4,
    allowFullscreen: false,
    canDelete: true,
    isLoading: false,
  };

  @Output()
  onAddWidget: EventEmitter<DashboardComponent> = new EventEmitter();

  @Output()
  onEditWidget: EventEmitter<WidgetChange> = new EventEmitter();

  @Output()
  onDeleteWidget: EventEmitter<WidgetChange> = new EventEmitter();

  @Output()
  onChangeDashboard: EventEmitter<DashboardChange> = new EventEmitter();

  @Output()
  onEditDashboard: EventEmitter<DashboardComponent> = new EventEmitter();

  @Output()
  onDeleteDashboard: EventEmitter<DashboardComponent> = new EventEmitter();

  @Output()
  onFreeze: EventEmitter<DashboardSettings> = new EventEmitter();

  @Output()
  onChangeStart: EventEmitter<WidgetChange> = new EventEmitter();

  @Output()
  onChangeEnd: EventEmitter<WidgetChange> = new EventEmitter();

  toggleFullscreen() {
    const elem = document.body as any;
    const doc = document as any;
    if (!this.fullScreen()) {
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.msRequestFullscreen) {
        elem.msRequestFullscreen();
      } else if (elem.mozRequestFullScreen) {
        elem.mozRequestFullScreen();
      } else if (elem.webkitRequestFullscreen) {
        elem.webkitRequestFullscreen((Element as any).ALLOW_KEYBOARD_INPUT);
      }
    } else if (doc.exitFullscreen) {
      doc.exitFullscreen();
    } else if (doc.msExitFullscreen) {
      doc.msExitFullscreen();
    } else if (doc.mozCancelFullScreen) {
      doc.mozCancelFullScreen();
    } else if (doc.webkitExitFullscreen) {
      doc.webkitExitFullscreen();
    }
  }

  fullScreen() {
    const doc: any = document;
    return !!(
      doc.fullscreenElement ||
      doc.mozFullScreenElement ||
      doc.webkitFullscreenElement ||
      doc.msFullscreenElement
    );
  }
}
