export function parse(moduleSrc) {
  const src = moduleSrc.split(/\n/)
    .map((l) => l.trim())
    .filter(Boolean)
    .filter((l) => !l.match(/^\/?\*/) && !l.match(/^\/\//))
    .join('\n');
  const fn = new Function(`var exports = {};
var module = { exports: exports };
${src}
return exports;`);
  return fn();
}
