import { Injectable } from '@angular/core';
import { PasswordStrength } from '@c8y/client';
import { filter } from 'lodash-es';
import { PasswordStrengthColor } from './password.model';
import { gettext } from '../i18n/gettext';
import { PasswordConfirmModalComponent } from './password-confirm-modal.component';
import { BsModalService } from 'ngx-bootstrap/modal';
import { take } from 'rxjs/operators';

@Injectable()
export class PasswordService {

  private GREEN: PasswordStrengthColor = {
    colorName: 'green',
    color: 'rgb(0, 128, 0)',
    description: gettext('strong'),
    passwordStrength: PasswordStrength.GREEN
  };
  private YELLOW: PasswordStrengthColor = {
    colorName: 'yellow',
    color: 'rgb(255, 204, 51)',
    description: gettext('medium'),
    passwordStrength: PasswordStrength.YELLOW
  };
  private RED: PasswordStrengthColor = {
    colorName: 'red',
    color: 'rgb(170, 0, 51)',
    description: gettext('weak'),
    passwordStrength: PasswordStrength.RED
  };

  constructor(private modalService: BsModalService) {}

  confirmPassword() {
    const modalInstance: any = this.modalService.show(PasswordConfirmModalComponent);
    return modalInstance.content.passwordConfirmedEmitter.pipe(take(1));
  }

  hasLowerCase(password: string) {
    return (password.search(/[a-z]/) !== -1);
  }

  hasUpperCase(password: string) {
    return (password.search(/[A-Z]/) !== -1);
  }

  hasNumbers(password: string) {
    return (password.search(/[0-9]/) !== -1);
  }

  hasSpecialChars(password: string) {
    return (password.search(/[^0-9a-zA-Z]+/) !== -1);
  }

  getStrengthColor(password: string): PasswordStrengthColor {
    const passwordStrength = filter([
      this.hasLowerCase(password),
      this.hasUpperCase(password),
      this.hasNumbers(password),
      this.hasSpecialChars(password)
    ]).length;

    if (passwordStrength > 2) {
      return this.GREEN;
    } else if (passwordStrength >= 2) {
      return this.YELLOW;
    } else {
      return this.RED;
    }
  }
}
