import { Component, Input, Output, EventEmitter } from '@angular/core';
import { IUser, UserService } from '@c8y/client';
import { AlertService } from '../alert/public-api';
import { gettext } from '../i18n/gettext';

@Component({
  selector: 'c8y-user-totp-revoke',
  templateUrl: './user-totp.component.html'
})
export class UserTotpRevokeComponent {
  @Input() user: IUser;
  @Output('revoke') revokeEmitter = new EventEmitter<undefined>();
  constructor(
    private users: UserService,
    private alert: AlertService,
  ) { }

  async revoke() {
    try {
      await this.users.totpRevokeSecret(this.user);
      this.alert.success(gettext('User TOTP secret revoked.'));
      this.revokeEmitter.next();
    } catch (e) {
      this.alert.addServerFailure(e);
    }
  }
}
