import {
  Directive,
  Input,
  Output,
  QueryList,
  AfterContentInit,
  ContentChildren,
  EventEmitter
} from '@angular/core';
import { map } from 'lodash-es';
import { InputGroupListComponent } from './input-group-list.component';

@Directive({
  selector: '[c8yInputGroupListContainer]'
})
export class InputGroupListContainerDirective implements AfterContentInit {
  @ContentChildren(InputGroupListComponent) listProjections: QueryList<InputGroupListComponent>;

  ngAfterContentInit() {
    this.listProjections.map(element => {
      if (element !== this.listProjections.last) {
        setTimeout(() => {
          element.plus = false;
        });
      }
    });

    this.listProjections.changes.subscribe(elements => {
      if (elements.length === 1) {
        setTimeout(() => {
          elements.first.plus = true;
          elements.first.minus = false;
        });

        return;
      }
      elements.map(element => {
        setTimeout(() => {
          element.minus = true;
        });

        if (element !== elements.last) {
          setTimeout(() => {
            element.plus = false;
          });
        }
        if (element === elements.last) {
          setTimeout(() => {
            element.plus = true;
          });
        }
      });
    });
  }
}
