import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { PickedDates } from './date-picker.model';

@Component({
  selector: 'c8y-date-picker',
  templateUrl: './date-picker.component.html'
})
export class DatePickerComponent implements OnInit {
  @Output() onDateSelected: EventEmitter<PickedDates> = new EventEmitter();

  fgDatePicker: FormGroup;

  ngOnInit() {
    this.fgDatePicker = new FormGroup({
      dateFrom: new FormControl(),
      dateTo: new FormControl()
    });
  }

  filter() {
    this.onDateSelected.emit(this.fgDatePicker.value);
  }

  clearFilter() {
    this.fgDatePicker.setValue({
      dateFrom: null,
      dateTo: null
    });
    this.onDateSelected.emit(null);
  }
}
