import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';

@Component({
  selector: 'c8y-list-item-radio, c8y-li-radio',
  templateUrl: './list-item-radio.component.html',
  host: {
    class: 'c8y-list__item__radio'
  }
})
export class ListItemRadioComponent {
  @ViewChild('radio', { static: true }) radio: ElementRef;
  @Input()
  selected = false;
  @Input()
  name = 'list_item_radio';
  @Input()
  disabled: boolean = false;
  @Output()
  onSelect = new EventEmitter<boolean>();

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.selected && !changes.selected.firstChange) {
      this.onSelect.emit(changes.selected.currentValue);
    }
  }

  reset() {
    this.radio.nativeElement.checked = false;
  }
}
