import { BsDropdownDirective } from 'ngx-bootstrap/dropdown';
import { gettext } from '../i18n/gettext';
import {
  Component,
  EventEmitter,
  HostListener,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';

export interface Item {
  name: string;
  _selected?: boolean;
  [key: string]: any;
}

export type selectedFunction = (item: Item) => boolean;
export type selectedLabelFunction = (items: Item[]) => string;

@Component({
  selector: 'c8y-select',
  templateUrl: './select.component.html'
})
export class SelectComponent implements OnChanges {
  @Input() placeholder: string = gettext('Select item');
  @Input() selectedLabel: string | selectedLabelFunction;
  @Input() applyLabel: string = gettext('Apply');
  @Input() items: Item[];
  @Input() selected: Item[] | selectedFunction;
  @Output() onChange: EventEmitter<Item[]> = new EventEmitter();
  @ViewChild(BsDropdownDirective, { static: false }) dropdown: BsDropdownDirective;
  textFilter = '';
  labelText = '';
  isOpen = false;
  filteredItems: Item[] = [];
  searchFilter = null;
  readonly sizeToShowFilter: number = 5;
  labelsForSelectAll: any = {
    all: gettext('All'),
    allFiltered: gettext('All filtered')
  };
  showAllLabel = false;
  private itemsSelected: Set<Item> = new Set();
  private stopClicks = false;

  @HostListener('click', ['$event'])
  preventClick(evt) {
    if (this.stopClicks) {
      evt.stopPropagation();
    }
    this.stopClicks = this.isOpen;
  }

  isOpenChange(isOpen: boolean) {
    this.isOpen = isOpen;
    if (isOpen) {
      this.updateSelected();
      this.searchFilter = null;
    } else {
      this.stopClicks = false;
    }
  }

  outterSelected(item: Item) {
    const { selected } = this;
    let isSelected: any = () => false;
    if (typeof selected === 'function') {
      isSelected = selected;
    } else if (Array.isArray(selected)) {
      isSelected = (i: Item) => selected.indexOf(i) > -1;
    }
    return isSelected(item);
  }

  isSelected(item: Item) {
    return this.itemsSelected.has(item);
  }

  isAllItemsSelected() {
    return this.itemsSelected.size === this.items.length;
  }

  isAllFilteredSelected() {
    return this.itemsSelected.size === this.filteredItems.length;
  }

  applyChanges() {
    const selected = Array.from(this.itemsSelected.values());
    this.onChange.emit(selected);
    this.dropdown.hide();
  }

  selectAll(checked: boolean) {
    this.filteredItems.forEach(item => this.onChangeItem(checked, item));
  }

  onChangeItem(checked: boolean, item: Item) {
    if (checked) {
      this.itemsSelected.add(item);
    } else {
      this.itemsSelected.delete(item);
    }
  }

  updateFiltered(term: string) {
    if (term) {
      const search = new RegExp(term, 'i');
      this.filteredItems = this.items.filter(({ name }) => search.test(name));
    } else {
      this.filteredItems = this.items;
    }
  }

  getSelectAllToggleStatus() {
    const label = this.getLabel();
    const checked = this.isAllSelected();
    const indeterminate = !checked && this.itemsSelected.size > 0;

    return { label, checked, indeterminate};
  }

  ngOnChanges(changes: SimpleChanges) {
    if (this.isOpen) {
      return;
    }
    if (changes.items || changes.selected || changes.applyLabel) {
      this.updateSelected();
      this.updateLabel();
      this.showAllLabel = this.isAllItemsSelected();
    }
  }

  private updateLabel() {
    const outterSelected = this.items.filter(i => this.outterSelected(i));
    if (typeof this.selectedLabel === 'string') {
      this.labelText = this.selectedLabel;
    } else if (typeof this.selectedLabel === 'function') {
      this.labelText = this.selectedLabel(outterSelected);
    } else {
      this.labelText = outterSelected.map(({ name }) => name).join(', ');
    }
  }

  private updateSelected() {
    const { itemsSelected, items } = this;
    itemsSelected.clear();
    items.forEach(item => {
      if (this.outterSelected(item)) {
        itemsSelected.add(item);
      }
    });
    this.filteredItems = items;
  }

  private isAllSelected() {
    if (this.getLabel() === this.labelsForSelectAll.allFiltered) {
      return this.isAllFilteredSelected();
    } else {
      return this.isAllItemsSelected();
    }
  }

  private getLabel() {
    return this.searchFilter ? this.labelsForSelectAll.allFiltered : this.labelsForSelectAll.all;
  }
}
