import { Directive, Input, ElementRef } from '@angular/core';

@Directive({
  selector: '[c8y-guide-href]'
})
export class GuideHrefDirective {
  @Input('c8y-guide-href')
  c8yGuideHref;

  private htmlAnchor: HTMLAnchorElement;

  set baseHref(href) {
    this.htmlAnchor.href = `${href}/${this.c8yGuideHref}`;
  }

  constructor(private elementRef: ElementRef) {
    this.htmlAnchor = this.elementRef.nativeElement as HTMLAnchorElement;
    if (!this.htmlAnchor.hasAttribute('target')) {
      this.htmlAnchor.setAttribute('target', '_blank');
    }
  }
}
