import { TemplateRef, Type } from '@angular/core';
import { matches, snakeCase } from 'lodash-es';
import { NavigatorNodeData } from './navigator-node-data';
import { PopoverConfirmComponent } from '../modal/popover-confirm.component';

export interface ClickOptions {
  icon?: boolean;
  expander?: boolean;
  open?: boolean;
  $event?: any; // TODO: add proper type
}

export class NavigatorNode {
  icon: string;
  iconTemplate?: TemplateRef<any>;
  iconComponent?: Type<any>;
  children: NavigatorNode[] = [];
  label;
  path: string;
  parents: NavigatorNode[] = [];
  loading?: boolean;
  routerLinkExact: boolean = true;
  open: boolean = false;
  hidden: boolean = false;
  draggable: boolean = false;
  droppable: boolean = false;
  dragged = false;
  draggedHover = false;
  confirm: PopoverConfirmComponent = undefined;
  private _priority: number = 0;
  private expandDragTimeout;

  get hasChildren() {
    return this.children.length > 0;
  }

  get id() {
    return 'navigator_node_' + snakeCase(this.label);
  }

  get priority() {
    if (this._priority) {
      return this._priority;
    } else {
      const childrenPriorities = this.children.map(({ priority }) => priority || 0);
      if (childrenPriorities.length) {
        return childrenPriorities.length ? Math.max(...childrenPriorities) : 0;
      }
      return 0;
    }
  }

  set priority(priority) {
    this._priority = priority;
  }

  constructor(data?: NavigatorNodeData) {
    this.update(data);
  }

  openOnStart(url: string) {
    return false;
  }

  add(node: NavigatorNode) {
    if (node === this) {
      throw new Error('Adding node to itself');
    }
    if (this.children.indexOf(node) === -1) {
      this.children.push(node);
    }
    if (node.parents.indexOf(this) === -1) {
      node.parents.push(this);
    }
    this.updateChildren();
  }

  remove(node: NavigatorNode) {
    const ix = this.children.indexOf(node);
    const pix = node.parents.indexOf(this);
    if (ix > -1) {
      this.children.splice(ix, 1);
    }
    if (pix > -1) {
      node.parents.splice(pix, 1);
    }
    this.updateChildren();
  }

  update(data?: NavigatorNodeData) {
    if (data) {
      Object.assign(this, data);
      if (data.hidden !== undefined) {
        this.parents.forEach(p => {
          p.updateHidden();
        });
      }
    }
  }

  find(predicate) {
    if (typeof predicate === 'string') {
      const compareLabel = predicate.toLocaleLowerCase();
      predicate = ({ label }) => compareLabel === label.toLowerCase();
    }
    if (typeof predicate === 'object') {
      predicate = matches(predicate);
    }
    if (typeof predicate !== 'function') {
      throw new Error('Invalid search predicate');
    }
    return this.children.reduce(
      (found, child) => found || child.find(predicate),
      this.children.find(predicate)
    );
  }

  empty() {
    this.children.length = 0;
  }

  click(options: ClickOptions = {}) {
    // do nothing
  }

  drop($event) {
    $event.stopPropagation();
    clearTimeout(this.expandDragTimeout);
  }

  dragStart($event) {
    $event.stopPropagation();
    // we can't pass a object to setData, so we do it via service
    // set data is still needed, to make the drag&drop work
    $event.dataTransfer.setData('node', 'node');
    this.dragged = true;
  }

  dragEnd($event) {
    $event.stopPropagation();
    this.dragged = false;
    $event.dataTransfer.clearData();
  }

  get canDrop() {
    return this.droppable;
  }

  get canNavigate() {
    return typeof this.path !== 'undefined';
  }

  dragEnter($event) {
    $event.preventDefault();
    $event.stopPropagation();
    this.draggedHover = true;
    if (!this.open) {
      this.expandDragTimeout = setTimeout(() => this.expand(), 1000);
    }
  }

  dragLeave($event) {
    $event.preventDefault();
    $event.stopPropagation();
    this.draggedHover = false;
    clearTimeout(this.expandDragTimeout);
  }

  expand() {
    if (!this.open) {
      this.open = true;
      this.click({ open: true, expander: true });
    }
  }

  traverse(callback) {
    if (this.children) {
      this.children.forEach(child => {
        callback(child);
        child.traverse(callback);
      });
    }
  }

  destroy() {
    // nothing todo here
  }

  protected updateChildren() {
    this.sort();
    this.updateHidden();
  }

  protected sort() {
    this.children.sort((a, b) => {
      if (a.priority > b.priority) {
        return -1;
      } else if (a.priority < b.priority) {
        return 1;
      } else if ((a.label || '').toLowerCase() < (b.label || '').toLowerCase()) {
        return -1;
      } else if ((a.label || '').toLowerCase() > (b.label || '').toLowerCase()) {
        return 1;
      } else {
        return 0;
      }
    });
  }

  protected updateHidden() {
    if (typeof this.path === 'undefined') {
      this.hidden = !this.children.some(({ hidden }) => !hidden);
    }
  }
}
