import {
  Component,
  ViewChild,
  Input,
  ViewContainerRef,
  AfterContentInit,
  OnDestroy
} from '@angular/core';
import { TabsService } from './tabs.service';
import { Tab } from './tab.model';

@Component({
  selector: 'c8y-tab',
  template: '<ng-template #tpl><ng-content></ng-content></ng-template>'
})
export class TabComponent implements AfterContentInit, OnDestroy, Tab {
  @ViewChild('tpl', { static: false }) template: ViewContainerRef;
  @Input() path: string;
  @Input() label: string = '';
  @Input() icon: string;
  @Input() priority: number;
  @Input() orientation: 'horizontal' | 'vertical';
  constructor(private tabs: TabsService) {}

  ngAfterContentInit() {
    setTimeout(() => this.tabs.add(this as Tab));
  }

  ngOnDestroy() {
    this.tabs.remove(this as Tab);
  }
}
