import { Injectable, InjectionToken, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { ExtensionPoint, fromTrigger, sortByPriority, stateToFactory } from '../common/extension-hooks';
import { StateService } from '../common/state-service.abstract';
import { Search } from './search.model';

/**
 * A hook to use for Multi Provider extension.
 */
export const HOOK_SEARCH = new InjectionToken('SearchFactory');

/**
 * A service which defines the dearch function.
 */
@Injectable({
  providedIn: 'root'
})
export class SearchService extends StateService implements ExtensionPoint<Search> {
  items$: Observable<Search[]>;
  readonly refreshTrigger = new Subject();
  protected state$ = new BehaviorSubject<Set<Search>>(new Set<Search>());

  constructor(
    private injector: Injector,
    router: Router
  ) {
    super();
    this.items$ = fromTrigger(router, this.refreshTrigger, [
      () => this.injector.get(HOOK_SEARCH, []),
      stateToFactory(this.state$)
    ]).pipe(
      map((items: Search[]) => sortByPriority(items)),
      shareReplay(1)
    );
  }

  /**
   * Refresh the extension factories
   */
  refresh() {
    this.refreshTrigger.next(1);
  }

  /**
   * Returns the current state.
   * @readonly
   * @returns The current set of actions.
   */
  get state() {
    return this.state$.value;
  }

  /**
   * Adds a new item to the search in the header.
   * @param item The item to add.
   */
  add(item: Search) {
    this.state.add(item);
    this.emitNewState();
  }

  /**
   * Removes an search from the header.
   * @param item The item to remove.
   */
  remove(item: Search) {
    this.state.delete(item);
    this.emitNewState();
  }
}
