/* tslint:disable:max-classes-per-file */

import { Directive, TemplateRef, Type } from '@angular/core';
import { Column, FilterPredicateFunction } from '../data-grid.model';

export interface FilteringFormRendererSpec {
  renderer: TemplateRef<FilteringFormRendererContext> | Type<any>;
  context: FilteringFormRendererContext;
}

export class FilteringFormRendererContext {
  property: Column;
  applyFilter: (filteringModifier: FilteringModifier) => void;
  resetFilter: () => void;
}

export interface FilteringModifier {
  filterPredicate?: string | FilterPredicateFunction;
  externalFilterQuery?: string | object;
}

@Directive({ selector: '[c8yFilteringFormRendererDef]' })
export class FilteringFormRendererDefDirective {
  constructor(public template: TemplateRef<any>) {}
}
