import { Output, EventEmitter, Component, Input, SimpleChanges } from '@angular/core';

@Component({
  selector: 'c8y-list-item-checkbox, c8y-li-checkbox',
  templateUrl: './list-item-checkbox.component.html',
  host: {
    class: 'c8y-list__item__check'
  }
})
export class ListItemCheckboxComponent {
  @Input()
  selected = false;
  @Output()
  onSelect = new EventEmitter<boolean>();

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.selected && !changes.selected.firstChange) {
      this.onSelect.emit(changes.selected.currentValue);
    }
  }
}
