import { Component, Input, OnDestroy } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { StatusType, Status, statusIcons, statusAlert } from '../common/status.model';
import { ModalLabels } from '../select-modal/select-modal.model';

/**
 * Labels to be used in the selection modal.
 * @deprecated This is going to be removed after 1007.7.0. Use ModalLabels instead.
 */
// tslint:disable-next-line: no-empty-interface
export interface ILabels extends ModalLabels {}

@Component({
  selector: 'c8y-confirm-modal',
  templateUrl: './modal-confirm.component.html'
})
export class ConfirmModalComponent implements OnDestroy {
  result: Promise<boolean>;
  dismiss: () => void;
  close: () => void;
  @Input() title: string;
  @Input() body: string;
  @Input() status: StatusType = Status.INFO;
  @Input() set labels(labels: ModalLabels) {
    const { ok = 'OK', cancel = 'Cancel' } = labels || {};
    this._labels = { ok, cancel };
  }

  get labels(): ModalLabels { return this._labels; }

  get classIcon() {
    return `fa-${statusIcons[this.status]}`;
  }

  get classAlert() {
    return statusAlert[this.status];
  }

  private _labels: ModalLabels;

  constructor(private modal: BsModalRef) {
    this._labels = {
      ok: 'OK',
      cancel: 'Cancel'
    };
    this.result = new Promise((resolve, reject) => {
      this.dismiss = () => {
        reject();
        this.modal.hide();
      };
      this.close = () => {
        resolve(true);
        this.modal.hide();
      };
    });
  }

  ngOnDestroy() {
    this.dismiss();
  }
}
