import {
  Component,
  Input,
  Output,
  QueryList,
  TemplateRef,
  ContentChild,
  EventEmitter
} from '@angular/core';

@Component({
  selector: 'c8y-input-group-list',
  templateUrl: 'input-group-list.component.html'
})
export class InputGroupListComponent {
  @Input() plus: boolean = true;
  @Input() minus: boolean = true;
  @Input() index: number;

  @Output() onRemove: EventEmitter<number> = new EventEmitter<number>();
  @Output() onAdd: EventEmitter<number> = new EventEmitter<number>();
  add() {
    this.onAdd.emit(this.index);
  }

  remove() {
    this.onRemove.emit(this.index);
  }
}
