import { ContentChild, Directive, Input } from '@angular/core';

import { CellRendererDefDirective, HeaderCellRendererDefDirective } from './cell-renderer';
import { FilteringFormRendererDefDirective } from './filtering-form-renderer';

@Directive({ selector: 'c8y-column' })
export class ColumnDirective {
  @Input() name: string;

  @ContentChild(CellRendererDefDirective, { static: false })
  cellRendererDef: CellRendererDefDirective;

  @ContentChild(HeaderCellRendererDefDirective, { static: false })
  headerCellRendererDef: HeaderCellRendererDefDirective;

  @ContentChild(FilteringFormRendererDefDirective, { static: false })
  filteringFormRendererDef: FilteringFormRendererDefDirective;
}
