import { Injectable, InjectionToken, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import {
  ExtensionPoint,
  fromTrigger,
  sortByPriority,
  stateToFactory
} from '../common/extension-hooks';
import { StateService } from '../common/state-service.abstract';
import { ActionBarItem } from './action-bar.model';

/**
 * A hook to use for Multi Provider extension.
 */
export const HOOK_ACTION_BAR = new InjectionToken('ActionBarFactory');

/**
 * A service which defines action-bar items.
 */
@Injectable({
  providedIn: 'root'
})
export class ActionBarService extends StateService implements ExtensionPoint<ActionBarItem> {
  items$: Observable<ActionBarItem[]>;
  readonly refreshTrigger = new Subject();
  protected state$ = new BehaviorSubject<Set<ActionBarItem>>(new Set<ActionBarItem>());

  constructor(private injector: Injector, router: Router) {
    super();
    this.items$ = fromTrigger(router, this.refreshTrigger, [
      () => this.injector.get(HOOK_ACTION_BAR, []),
      stateToFactory(this.state$)
    ]).pipe(
      map((items: ActionBarItem[]) => sortByPriority(items)),
      shareReplay(1)
    );
  }

  /**
   * Returns the current state.
   * @readonly
   * @returns The current set of actions.
   */
  get state(): Set<ActionBarItem> {
    return this.state$.value;
  }

  refresh() {
    this.refreshTrigger.next();
  }

  /**
   * Adds a new item to the action bar in the header and emits a state change.
   * @param item The item to add.
   */
  add(item: ActionBarItem) {
    this.state.add(item);
    this.emitNewState();
  }

  /**
   * Removes an action bar item from the header and emits a state change.
   * @param item The item to remove.
   */
  remove(item: ActionBarItem) {
    this.state.delete(item);
    this.emitNewState();
  }
}
