import { InjectionToken } from '@angular/core';
import { ExtensionFactory } from '../common/public-api';

export interface DocLink {
  icon: string;
  label: string;
  url: string;
  type: 'doc' | 'quicklink';
  click?: any;
}

export type DocLinkFactory = ExtensionFactory<DocLink>;

export const HOOK_DOCS = new InjectionToken<ExtensionFactory<DocLink>>('Hook docs');
