import { Injectable } from '@angular/core';
import { IOperationBulk, Realtime } from '@c8y/client';
import { RealtimeService } from './realtime.service';

/**
 * A service for handling bulk operation realtime notifications in RxJS fashion.
 */
@Injectable()
export class OperationBulkRealtimeService extends RealtimeService<IOperationBulk> {
  constructor(realtime: Realtime) {
    super(realtime);
  }

  protected channel(): string {
    return '/bulkOperations/*';
  }
}
