import { Component, Input, Output, TemplateRef, EventEmitter } from '@angular/core';
import { StepState } from '@angular/cdk/stepper';

@Component({
  selector: 'c8y-stepper-progress',
  templateUrl: './stepper-progress.component.html'
})
export class C8yStepperProgress {
  /** List of overriden icons as TemplateRef. Also default icons could be overriden.
   *  The given overriden icons will replace an icon with matching state: StepState
   *
   * <ng-container
   * *ngSwitchCase="true"
   * [ngTemplateOutlet]="iconOverrides[state]"
   * ></ng-container>
   *
   */
  @Input() iconOverrides: { [key: string]: TemplateRef<any> };
  /** The index of the current step */
  @Input() index: number;
  /** Whether the step is cuurently selected or not  */
  @Input() selected: boolean;
  /** The current state of the step */
  @Input() state: StepState;

  /** Emits the index: number of the step which was clicked */
  @Output() onStepClicked = new EventEmitter<number>();

  /** Called on click and emits the step index */
  stepSelected() {
    this.onStepClicked.emit(this.index);
  }
}
