import { Type } from '@angular/core';
import { IResultList } from '@c8y/client';

/*
 * For reference: the structure of current device column-related objects.
 * TODO: Can be removed when "All devices" is fully migrated.
 *
 * Services involved:
 * - c8yFilteringSortingColumns
 * - c8yFilteringSortingInventoryQueries
 * - c8yDeviceListColumns
 * - c8yFilteringSortingCollections?? -> for other tables in tenant/cell ID usage statistics feature
 *
 * Components involved:
 * - c8yFilteredSortedList -> this needs to be gravely adapted
 *
 * States saved in user preferences:
 * - Columns config (ALL_DEVICES_COLUMNS_CONFIG_USER_PREFERENCES_KEY)
 * - Columns meta (ALL_DEVICES_COLUMNS_META_USER_PREFERENCES_KEY_PREFIX)
 *
 * States saved in dynamic group object:
 * - Columns config (COLUMNS_CONFIG_FRAGMENT)
 * - Columns meta (COLUMNS_META_FRAGMENT)
 * - Query string (QUERY_STRING_FRAGMENT)
 *
 * TO-FUTURE-SELF note:
 * - `col.config.filtering` and `col.config.sorting` (the models) populate
 * `col.filtering` and `col.sorting` (the views).
 * - `getFilter` can return a query object (for server-side filtering) or
 * a filter function (for server-side filtering).
 */
interface DeviceColumn {
  name: string;
  header: string;
  config: {
    filtering: object;
    sorting: {
      order: number;
    };
    override: DeviceColumn;
  };
  cell: {
    class: string;
    innerClass: string;
    scopeExtensions: object;
    template: string;
    style: object;
  };
  filtering: {
    form: object[];
    schema: {
      type: string;
      properties: {
        type: string;
        [key: string]: any;
      };
    };
    getFilter(filteringModel: object): object | FilterFunction;
  };
  sorting: {
    path?: string;
    paths?: string;
  };
  meta: {
    custom: boolean;
    key: string;
    headerName: string;
    fragmentPath: string;
    active: boolean;
    filteringMode: 'EQUALITY' | 'EXISTENCE';
  };
}

type FilterFunction = (item: object) => boolean;

export interface DataGrid {
  columns: Column[];
  rows: Row[];
  pagination: Pagination;
  serverSideDataCallback: ServerSideDataCallback;
  selectable: boolean;
  selectionPrimaryKey: string;
  displayOptions: DisplayOptions;
  actionControls: ActionControl[];
  bulkActionControls: BulkActionControl[];
}

/*
 * Column
 */
export interface Column {
  name: string | SpecialColumnName;
  header?: string;
  path?: string;
  visible?: boolean;
  positionFixed?: boolean;

  /*
   * Cosmetics
   */
  dataType?: ColumnDataType;
  gridTrackSize?: string;
  headerCSSClassName?: string | string[];
  cellCSSClassName?: string | string[];

  /*
   * Sorting-Filtering
   */
  sortable?: boolean;
  sortOrder?: SortOrder;

  filterable?: boolean;
  filterPredicate?: string | FilterPredicateFunction;
  externalFilterQuery?: any;

  /*
   * Custom renderer
   */
  cellRendererComponent?: Type<any>;
  headerCellRendererComponent?: Type<any>;
  filteringFormRendererComponent?: Type<any>;
}

export const enum SpecialColumnName {
  Checkbox = 'checkbox',
  Actions = 'actions'
}

export const enum ColumnDataType {
  Icon = 'icon',
  Numeric = 'numeric',
  TextShort = 'text-short',
  TextLong = 'text-long'
}

export const enum ColumnDataRecordClassName {
  Icon = 'data-record-icon',
  Header = 'data-record-header',
  Default = ''
}

export const minColumnGridTrackSize = 80;

export const ratiosByColumnTypes = {
  icon: 1,
  numeric: 1,
  'text-short': 1.67,
  'text-long': 3.33
};

export type SortOrder = 'asc' | 'desc' | '';

export type FilterPredicateFunction = (item: object, path: string) => boolean;

export enum FilteringActionType {
  ApplyFilter = 'APPLY_FILTER',
  ResetFilter = 'RESET_FILTER'
}

export interface Row {
  id: string;
  [key: string]: any;
}

export interface Pagination {
  currentPage: number;
  nextPage?: number;
  pageSize: number;
}

export type ServerSideDataCallback = (
  dataSourceModifier: DataSourceModifier
) => ServerSideDataResult;
export interface DataSourceModifier {
  columns: Column[];
  searchText: string;
  pagination: Pagination;
  selection: {
    enabled: boolean;
    primaryKey: string;
  };
}

/**
 * Describes result from server with data and additional statistics.
 */
export type ServerSideDataResult = IResultList<object> & {
  /** The real grand total number of items (the whole dataset). */
  size: number;

  /** The number of items after filtering applied to the whole dataset (subset). */
  filteredSize: number;

  /** The list of ids of all filtered items (subset). */
  filteredDataIds?: string[];
};

export interface DataSourceStats {
  /** The real grand total number of items (the whole dataset). */
  size: number;

  /** The number of items after filtering applied to the whole dataset (subset). */
  filteredSize: number;

  /** The number of the current page. */
  currentPage: number;

  /** The number of the next page. */
  nextPage?: number;

  /** The number of items currently shown in the list view, a.k.a. current page (what the user sees on screen). */
  currentPageSize: number;

  /** The number of items on the first page.  */
  firstPageSize: number;
}

export interface DisplayOptions {
  striped: boolean;
  bordered: boolean;
  gridHeader: boolean;
}

export interface ActionControl {
  type: BuiltInActionType | string;
  text?: string;
  icon?: string;
  // tslint:disable-next-line:ban-types
  callback: ((item: Row, reload: () => void) => void) | Function;
  [key: string]: any;
}

export interface BulkActionControl extends ActionControl {
  callback(selectedItemIds: string[], reload: () => void): void;
}

export const enum BuiltInActionType {
  Edit = 'EDIT',
  Delete = 'DELETE',
  Export = 'EXPORT'
}
