import { Directive, forwardRef, Attribute } from '@angular/core';
import { NG_VALIDATORS, Validator, AbstractControl } from '@angular/forms';

@Directive({
  selector: '[passwordConfirm]',
  providers: [
    {
      provide: NG_VALIDATORS,
      useExisting: PasswordConfirm,
      multi: true
    }
  ]
})

export class PasswordConfirm implements Validator {

  constructor(@Attribute('passwordConfirm') public passwordConfirm: string) {
  }

  validate(abControl: AbstractControl): { [key: string]: any } {
    const value = abControl.value;
    const controlToCompareWith = abControl.root.get(this.passwordConfirm);
    if (controlToCompareWith && value !== controlToCompareWith.value) {
      return { passwordConfirm: true };
    }
    return null;
  }
}
