import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Subject, timer } from 'rxjs';
import { debounce, distinctUntilChanged, map } from 'rxjs/operators';

@Component({
  selector: 'c8y-filter',
  templateUrl: './filter-input.component.html'
})
export class FilterInputComponent implements OnInit, OnDestroy {
  @Input() icon: string;
  @Output() onSearch = new EventEmitter<string>();
  filterChange$ = new Subject();
  filterTerm: string = '';
  private readonly CARRIAGE_RETURN_KEY = 13;
  private readonly CARRIAGE_RETURN_CODE = 'Enter';

  ngOnInit() {
    this.reset();
    this.filterChange$
      .pipe(
        debounce((event: any) =>
          event.code === this.CARRIAGE_RETURN_CODE || event.keyCode === this.CARRIAGE_RETURN_KEY
            ? timer(10)
            : timer(300)
        ),
        map((e: any) => e.target.value),
        distinctUntilChanged()
      )
      .subscribe(filterTerm => {
        this.filterTerm = filterTerm;
        this.onSearch.emit(filterTerm);
      });
  }

  reset() {
    this.filterTerm = '';
    this.onSearch.emit('');
  }

  ngOnDestroy(): void {
    this.filterChange$.complete();
  }
}
