import { Component, OnInit, ContentChildren, QueryList } from '@angular/core';
import { GuideHrefDirective } from './guide-href.directive';
import { DocsService } from './docs.service';

@Component({
  selector: '[c8y-guide-docs]',
  templateUrl: './guide-docs.component.html'
})
export class GuideDocsComponent implements OnInit {
  baseUrl: string;
  @ContentChildren(GuideHrefDirective, { descendants: true }) links: QueryList<GuideHrefDirective>;

  constructor(private docs: DocsService) {}

  ngOnInit(): void {
    this.baseUrl = this.docs.getBaseUrl();
  }

  ngAfterViewInit(): void {
    this.links.forEach(guide => {
      guide.baseHref = this.baseUrl;
    });
  }
}
