import { Component, Input, ViewChild, ElementRef } from '@angular/core';
import { Search } from './search.model';
import { sortBy } from 'lodash';
import { AlertService } from '../alert/alert.service';
import { Alert } from '../alert/alert.model';

@Component({
  selector: 'c8y-search-outlet',
  templateUrl: './search-outlet.component.html'
})
export class SearchOutletComponent {
  @Input() search: Search[] = [];

  constructor(private alertService: AlertService) {}

  openSearch() {
    setTimeout(() => this.focusInputBox(), 100);
  }

  getByPriority() {
    return sortBy(this.search, this.byPriority);
  }

  closeDropdown(dropdown) {
    dropdown.hide();
  }

  verifyNotEmpty(term) {
    if (!term || term === '') {
      this.alertService.add({ text: 'Type something to execute a search.', type: 'warning' });
    }
  }

  private byPriority(item) {
    return -item.priority;
  }

  private focusInputBox() {
    const elementToFocus: any = document.querySelector('#search-header-menu input');
    if (elementToFocus) {
      elementToFocus.focus();
    }
  }
}
