import { Component, Input } from '@angular/core';

@Component({
  selector: 'c8y-right-drawer',
  templateUrl: './right-drawer.component.html'
})
export class RightDrawerComponent {
  @Input('quickLinks') set _quickLinks(data: any[]) {
    if (data) {
      this.quickLinks = data.filter(d => d.type === 'quicklink');
    }
  }
  @Input('helpAndSupport') set _helpAndSupport(data: any[]) {
    if (data) {
      this.helpAndSupport = data.filter(d => d.type === 'doc');
    }
  }
  @Input() app: any;
  quickLinks: any[] = [];
  helpAndSupport: any[] = [];
}
