import { Component, Input, Optional, Output, EventEmitter, HostListener } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { gettext } from '../i18n/gettext';
import { isEmpty } from 'lodash';
import { ModalLabels } from '../select-modal/select-modal.model';

/**
 * C8y modal component.
 * ## Example:
 *
 * Creating custom modal:
 * ```typescript
 *  import { Component } from "@angular/core";
 *  import { Subject } from 'rxjs';
 *
 * @Component({
 *  selector: "my-modal",
 *  template: `
 *    <c8y-modal title="CustomTitle"
 *      (onClose)="onClose($event)"
 *      (onDismiss)="onDismiss($event)"
 *      [labels]="labels"
 *      [disabled]="true" <- will disable ok button
 *      >
 *        <span>I am body of modal</span>
 *        <span>For simple string use body="string"</span>
 *    </c8y-modal>`
 * })
 *
 * export class MyModalComponent {
 *
 *  closeSubject: Subject<boolean> = new Subject();
 *  labels : ModalLabels = {ok: "customOK", cancel: "customCancel"};
 *
 *  onDismiss(event){
 *    this.closeSubject.next(false);
 *  }
 *
 *  onClose(event) {
 *    this.closeSubject.next(true);
 *  }
 * }
 * ```
 * Showing modal:
 * ```typescript
 * import { BsModalService } from "ngx-bootstrap/modal";
 *
 * constructor(
 *  public bsModalService: BsModalService,
 * ) {}
 *
 * showModal() {
 *  const modalRef = this.bsModalService.show(MyModalComponent);
 *  modalRef.content.closeSubject.subscribe(result => {
 *    console.log('results:', result);
 *  });
 * }
 * ```
 */
@Component({
  selector: 'c8y-modal',
  templateUrl: './modal.component.html',
})

export class ModalComponent {
  @Output() onDismiss: EventEmitter<boolean> = new EventEmitter();
  @Output() onClose: EventEmitter<boolean> = new EventEmitter();
  @Input() disabled = false;
  @Input() close: () => void;
  @Input() dismiss: () => void;
  @Input() title: string;
  @Input() body: string;
  @Input() customFooter: boolean = false;
  @Input() set labels({ok, cancel}: ModalLabels) {
    this._labels = {ok , cancel};
  }

  get labels(): ModalLabels { return this._labels; }

  private _labels: ModalLabels =  { ok: gettext('OK'), cancel: gettext('Cancel') };

  constructor(@Optional() private modal: BsModalRef) {}

  _dismiss() {
    let fn = this.dismiss;
    if (!fn && this.modal) { fn = () => this.modal.hide(); }
    if (fn) { fn(); }
    this.onDismiss.emit(true);
  }

  _close() {
    let fn = this.close;
    if (!fn && this.modal) { fn = () => this.modal.hide(); }
    if (fn) { fn(); }
    this.onClose.emit(true);
  }

  @HostListener('document:keydown.enter', ['$event']) onEnterKeyDown(event: KeyboardEvent) {
    const hasOnlyOneButton = isEmpty(this.labels.ok) !== isEmpty(this.labels.cancel);
    const isCancelClickable = !this.customFooter && this.labels.cancel;
    const isOkClickable = !this.customFooter && !this.disabled && this.labels.ok;
    if (hasOnlyOneButton  && isCancelClickable) {
      this._dismiss();
    }
    if (hasOnlyOneButton  && isOkClickable) {
      this._close();
    }
  }
}
