import { Injector, Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router } from '@angular/router';
import { from, isObservable, of, Observable } from 'rxjs';
import { every, filter, map, mergeMap, toArray, tap, debounceTime } from 'rxjs/operators';
import { Tab } from '../tabs/tab.model';
import { Route } from './router.models';

@Injectable()
export class RouterTabsResolver {
  constructor(private injector: Injector, private router: Router) {}

  get routerStateSnapshot() {
    return this.router.routerState.snapshot;
  }

  resolve(route: ActivatedRouteSnapshot) {
    const latestChildren = this.router.config.find(r => route.routeConfig.path === r.path).children;
    return from(latestChildren || []).pipe(
      mergeMap((child: Route) =>
        from(this.getGuards(child)).pipe(
          map((guard: CanActivate) => guard.canActivate(route, this.routerStateSnapshot)),
          mergeMap((result: any) => {
            if (isObservable(result)) {
              return result;
            }
            if (result && typeof result.then === 'function') {
              return from(result);
            }
            return of(result);
          }),
          tap(result => {
            if (Array.isArray(result)) {
              child.tabs = result;
            }
          }),
          every(result => !!result),
          filter(result => result),
          mergeMap(() => (Array.isArray(child.tabs) ? from(child.tabs) : of(child as Tab))),
          map(result =>
            result.label
              ? ({
                  label: result.label,
                  icon: result.icon,
                  path: `${route.url.join('/')}/${result.path}`,
                  priority: result.priority
                } as Tab)
              : undefined
          )
        )
      ),
      filter(tabs => !!tabs),
      toArray()
    ) as Observable<Tab[]>;
  }

  private getGuards(child: Route) {
    const guards = (child.canActivate || []).map(guard => {
      return this.injector.get(guard);
    });
    if (child.data && child.data.showIf) {
      guards.push({ canActivate: child.data.showIf });
    }
    return guards;
  }
}
