import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { NewPassword } from './password.model';
import { SystemOptionsService } from '@c8y/client';
import { ControlContainer, NgForm } from '@angular/forms';

@Component({
  selector: 'c8y-new-password',
  templateUrl: './new-password.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class NewPasswordComponent implements OnInit {
  @Output() password = new EventEmitter<NewPassword | {}>();
  model: any = {};
  enforcePasswordStrength: boolean = false;
  changePassword: boolean = false;

  constructor(private systemOptions: SystemOptionsService) {}

  ngOnInit() {
    this.loadEnforcePasswordStrength();
  }

  newPasswordChanged() {
    this.password.emit({
      password: this.model.newPassword,
      passwordStrength: this.model.strength
    });
  }

  async loadEnforcePasswordStrength() {
    try {
      const { data } = await this.systemOptions.detail({
        category: 'password',
        key: 'enforce.strength'
      });
      this.enforcePasswordStrength = data.value === 'true';
    } catch (e) {
      // do nothing;
    }
  }

  toggleChangePassword() {
    this.changePassword = !this.changePassword;
    if (!this.changePassword) {
      this.password.emit({});
      this.model = {};
    }
  }
}
