import {
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  Input,
  Output,
  TemplateRef
} from '@angular/core';
import { IIdentified, Paging } from '@c8y/client';

@Component({
  selector: 'c8y-load-more',
  templateUrl: './load-more.component.html'
})
export class LoadMoreComponent {
  @Input()
  paging: Paging<any>;
  @Input()
  useIntersection = true;
  @Input()
  hidden = false;
  @Input()
  container: ElementRef;
  @Input()
  class: string = 'c8y-list__item bg-transparent';
  @Input()
  maxIterations = 10;
  @Input()
  noMoreDataHint: TemplateRef<any>;
  @Input()
  loadNextLabel: string;
  @Input()
  loadingLabel: string;
  @Output()
  onLoad = new EventEmitter<IIdentified>();

  isLoading = false;
  counter = 0;
  showNoMoreDataHint = false;
  private loadUntilIntersected;
  private readonly LOAD_SAME_PAGE_THRESHOLD = 50;
  private intersectionObserver: IntersectionObserver;

  @HostBinding('class')
  get hostClass() {
    return this.hidden || (!this.hasMore && !this.showNoMoreDataHint) ? '' : this.class;
  }

  get hasMore() {
    return (
      this.paging && (this.paging.totalPages > this.paging.currentPage || !!this.paging.nextPage)
    );
  }

  constructor(private element: ElementRef) {}

  ngAfterContentInit(): void {
    if (this.useIntersection && 'IntersectionObserver' in window) {
      this.intersectionObserver = new IntersectionObserver(event => this.buttonInView(event[0]), {
        root: this.container ? this.container.nativeElement : null
      });
      this.intersectionObserver.observe(this.element.nativeElement);
    }
    this.showNoMoreDataHint = this.shouldShowNoMoreDataHint();
  }

  ngOnDestroy(): void {
    if (this.intersectionObserver) {
      this.intersectionObserver.disconnect();
    }
  }

  async loadMore(event?) {
    this.isLoading = true;
    if (event) {
      event.stopPropagation();
    }
    if (this.hasMore) {
      const result = await this.paging.next();
      this.counter++;
      this.paging = result.paging;
      this.onLoad.emit(result.data);
      this.intersectionLoading();
      this.showNoMoreDataHint = this.shouldShowNoMoreDataHint();
    } else {
      this.counter = 0;
      this.isLoading = false;
    }
  }

  private intersectionLoading() {
    if (this.useIntersection && this.hasMore && this.loadUntilIntersected !== null) {
      this.loadUntilIntersected = setTimeout(() => this.loadMore(), this.getLoadingThreshold());
      this.useIntersection = this.shouldSwitchMode();
    } else {
      this.isLoading = false;
      this.loadUntilIntersected = undefined;
    }
  }

  private getLoadingThreshold(): number {
    return this.LOAD_SAME_PAGE_THRESHOLD * this.counter;
  }

  private shouldShowNoMoreDataHint() {
    return (this.counter !== 0 || this.noMoreDataHint) && !this.hasMore && !this.hidden;
  }

  private shouldSwitchMode() {
    return this.counter < this.maxIterations || this.hidden;
  }

  private buttonInView(event) {
    if (event.isIntersecting) {
      this.loadMore();
    } else if (this.loadUntilIntersected) {
      clearTimeout(this.loadUntilIntersected);
      this.loadUntilIntersected = null;
      this.isLoading = false;
    } else {
      // avoiding a race condition when timeout is faster
      // cleared then set
      this.loadUntilIntersected = null;
    }
  }
}
