import { Injectable, Injector } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate } from '@angular/router';
import { from, of } from 'rxjs';
import { catchError, map, shareReplay, tap } from 'rxjs/operators';
import { ViewContextServices } from './view-context.service';
import { InventoryService } from '../../api';

@Injectable()
export class ContextRouteGuard implements CanActivate {
  private caches = new Map();
  constructor(private injector: Injector) {}

  canActivate(route: ActivatedRouteSnapshot) {
    const id = route.paramMap.get('id');
    const service = this.service(route);
    this.clearDetailCached(service, id);
    return this.detailCached(service, id).pipe(
      tap(contextData => (route.data = { ...route.data, contextData })),
      map(() => true),
      catchError(ex => of(false))
    );
  }

  private service(route) {
    const serviceInstance = ViewContextServices.contextToService(route.data.context);
    return this.injector.get(serviceInstance);
  }

  private detailCached(service, id) {
    const cache = this.getCache(service);
    let cached = cache[id];
    if (!cached) {
      const params = service instanceof InventoryService ? { withChildren: false } : undefined;
      cached = from(service.detail(id, params)).pipe(
        map(({ data }) => data),
        shareReplay(1)
      );
      cache[id] = cached;
    }
    return cached;
  }

  private clearDetailCached(service, id) {
    const cache: Map<any, any> = this.getCache(service);
    delete cache[id];
  }

  private getCache(service) {
    let cache = this.caches.get(service);
    if (!cache) {
      cache = {};
      this.caches.set(service, cache);
    }
    return cache;
  }
}
