import { Injectable, Inject, Optional } from '@angular/core';
import { OptionsService } from '../common/options.service';
import { documentationItems } from './defaults.items';
import { AppStateService } from '../common/ui-state.service';
import { gettext } from '../i18n/gettext';
import { DocLink, HOOK_DOCS, DocLinkFactory } from './docs.models';
import { ExtensionPoint, fromTrigger } from '../common/public-api';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { shareReplay, startWith, first, filter } from 'rxjs/operators';
import { isUndefined } from 'lodash-es';

@Injectable({
  providedIn: 'root'
})
export class DocsService implements ExtensionPoint<DocLink> {
  items$: Observable<DocLink[]>;
  constructor(
    private options: OptionsService,
    private app: AppStateService,
    @Optional() @Inject(HOOK_DOCS) factories: DocLinkFactory[] = [],
    router: Router
  ) {
    if (!factories) {
      factories = [];
    }
    factories.push(this);
    const refreshTrigger = this.app.map(({ supportUrl }) => supportUrl);
    this.items$ = fromTrigger(router, refreshTrigger, factories).pipe(
      startWith([]),
      shareReplay(1)
    );
  }

  getBaseUrl(): string {
    return this.options.get('docsBaseUrl', 'https://www.cumulocity.com/guides');
  }

  get templateStr(): string {
    return this.options.get('guideHrefTemplate', '${docsBaseUrl}${partialUrl}');
  }

  getUserGuideLink(link) {
    if (/^https?:/.test(link)) {
      return link;
    }
    if (this.getBaseUrl === null) {
      return null;
    }
    return this.getLink(this.templateStr, link);
  }

  list() {
    return this.items$
      .pipe(
        filter(i => !!i.length),
        first()
      )
      .toPromise();
  }

  refresh() {
    // no op
  }

  get() {
    // use the function as a factory
    const { links, noDefault, excludeDefault = [] } = this.options.get('docs', {});
    const { supportUrl } = this.app.state;
    let staticLinks = noDefault
      ? []
      : documentationItems
          .map((item: DocLink) => ({ ...item, url: this.getUserGuideLink(item.url) }))
          .filter(({ url }) => !excludeDefault.some(e => new RegExp(e).test(url)));

    if (links) {
      // backwards compatibility
      links.map((lnk: DocLink) => {
        if (isUndefined(lnk.type)) {
          lnk.type = 'doc';
          return lnk;
        }
      });
      staticLinks = staticLinks.concat(links);
    }
    if (supportUrl) {
      staticLinks.push({
        icon: 'comments',
        label: gettext('Forum support'),
        url: supportUrl,
        type: 'doc'
      });
    }
    return staticLinks;
  }

  private getLink(templateStr, partialLink) {
    if (!templateStr) {
      return undefined;
    }
    return templateStr
      .replace(/\${docsBaseUrl}/, this.getBaseUrl())
      .replace(/\${partialUrl}/, this.prefixWithSlash(partialLink));
  }

  private prefixWithSlash(partialLink = '') {
    const shouldPrefix = !(partialLink && /^\//.test(partialLink));
    const prefix = shouldPrefix ? '/' : '';
    return `${prefix}${partialLink}`;
  }
}
