import { Component } from '@angular/core';
import { UserService } from '@c8y/client';
import { Status } from '../common/status.model';
import { gettext } from '../i18n/gettext';
import { ModalService } from '../modal/modal.service';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { LoginService } from '../login/public-api';
import { AlertService } from '../alert/public-api';

@Component({
  selector: 'c8y-user-totp-setup',
  templateUrl: './user-totp-setup.component.html'
})
export class UserTotpSetupComponent {
  constructor(
    private user: UserService,
    private modalService: ModalService,
    private modal: BsModalRef,
    private loginService: LoginService) { }

  async totpSetupVerified(token) {
    await this.user.activateTotp();
    this.modal.hide();
    try {
      await this.modalService.acknowledge(
        gettext('Logout required'),
        gettext('You must log out in order to apply your changes'),
        Status.WARNING,
        gettext('Log out')
      );
      await this.loginService.logout();
    } catch (ex) {
      // intended empty
    }
  }

  close() {
    this.modal.hide();
  }
}
