import { Component, Input, ViewChild, EventEmitter, Output } from '@angular/core';
import { UserMenuItem } from './user.model';
import { UserMenuService } from './user-menu.service';

@Component({
  selector: 'c8y-user-menu-item',
  templateUrl: './user-menu-item.component.html'
})
export class UserMenuItemComponent implements UserMenuItem  {
  @Input()
  icon: string;
  @Input()
  label: string;
  @Input()
  link: string;
  @Input()
  target: string;
  @Input()
  priority = 0;
  @ViewChild('template', {static: false}) template: any;

  @Output()
  click = new EventEmitter<UserMenuItem>();
  private viewInitTimeout;

  constructor(private userService: UserMenuService) { }

  ngAfterViewInit() {
    this.viewInitTimeout = setTimeout(() =>
      this.userService.add(this as UserMenuItem)
    );
  }

  ngOnDestroy() {
    clearTimeout(this.viewInitTimeout);
    this.userService.remove(this as UserMenuItem);
  }

  onClick() {
    this.click.emit(this);
  }
}
