import { Component, Input, EventEmitter, SimpleChanges, Output } from '@angular/core';
import { gettext } from '../i18n/gettext';
import { BsModalRef } from 'ngx-bootstrap';
import {
  ISelectModalObject,
  ModalSelectionMode,
  ModalLabels
} from './select-modal.model';
import { assign } from 'lodash-es';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { IIdentified } from '@c8y/client';

interface ISelectModalInternalObject extends ISelectModalObject {
  selectedId: string | number;
}

@Component({
  selector: 'c8y-select-modal',
  templateUrl: './select-modal.component.html'
})
export class SelectModalComponent {
  @Input() icon: string;
  @Input() title: string;
  @Input() subTitle: string = gettext('Select from the list of items matching the device type');
  @Input() items: ISelectModalInternalObject[];
  @Input() mode: ModalSelectionMode = ModalSelectionMode.MULTI;
  @Input() disableSelected: boolean = true;
  @Input() showFilter: boolean = true;
  @Input() areMoreEntries: boolean = false;
  @Input() set labels(labels: ModalLabels) {
    const { ok = this.labels.ok, cancel = this.labels.cancel } = labels || {};
    this._labels = { ok, cancel };
  }
  get labels(): ModalLabels {
    return this._labels;
  }
  @Output() result: EventEmitter<IIdentified | IIdentified[]> = new EventEmitter<
    IIdentified | IIdentified[]
  >();
  @Output() search: EventEmitter<string> = new EventEmitter<string>();
  selected = false;
  filterTerm: string = '';
  listItems = [];
  private debouncer: Subject<string> = new Subject<string>();
  private _labels: ModalLabels = { ok: gettext('Confirm'), cancel: gettext('Cancel') };

  constructor(private bsModalRef: BsModalRef) {
    this.debouncer.pipe(debounceTime(500)).subscribe(value => {
      this.search.emit(value);
    });
  }

  async ngOnChanges(changes: SimpleChanges) {
    if (changes.items && changes.items.currentValue) {
      const itemsPromise = changes.items.currentValue.map(async item => {
        item.options = await item.options;
        const selected = item.options.find(option => option.selected);
        if (selected) {
          item.selectedId = selected.obj.id;
          if (this.disableSelected) {
            item.options.map(option => assign(option, { disabled: true }));
          }
        }
        return item;
      });
      this.listItems = await Promise.all(itemsPromise);
    }
  }

  updatePipe(filterTerm: string) {
    this.debouncer.next(filterTerm);
    this.filterTerm = filterTerm;
  }

  updateChoice({ item, id }) {
    if (this.mode === 'single') {
      this.listItems.map(value => (value.selectedId = undefined));
    }
    item.selectedId = id;
    this.selected = true;
  }

  dismiss() {
    this.bsModalRef.hide();
  }

  select() {
    const outputArray = this.getOutput();
    const output = this.mode === 'single' ? outputArray[0] : outputArray;
    this.result.emit(output);
    this.bsModalRef.hide();
  }

  ngOnDestroy() {
    this.debouncer.complete();
    this.result.complete();
    this.search.complete();
  }

  private getOutput(): IIdentified | IIdentified[] {
    return this.listItems
      .filter(item => item.selectedId)
      .map(item => item.options.find(option => item.selectedId === option.obj.id))
      .map(selectedOption => selectedOption.obj);
  }
}
