/* tslint:disable:max-classes-per-file */

import { Directive, TemplateRef, Type } from '@angular/core';

import { Column } from '../data-grid.model';

export interface CellRendererDef {
  template: TemplateRef<any>;
}

export interface CellRendererSpec {
  renderer: TemplateRef<CellRendererContext> | Type<any>;
  context: CellRendererContext;
}

export class CellRendererContext {
  value: any;
  item?: any;
  property: Column;
}

@Directive({ selector: '[c8yCellRendererDef]' })
export class CellRendererDefDirective implements CellRendererDef {
  constructor(public template: TemplateRef<any>) {}
}

@Directive({ selector: '[c8yHeaderCellRendererDef]' })
export class HeaderCellRendererDefDirective implements CellRendererDef {
  constructor(public template: TemplateRef<any>) {}
}
