import { Component, ElementRef, NgZone } from '@angular/core';
import { HeaderService } from '../header.service';
import { debounceTime, map, filter } from 'rxjs/operators';

@Component({
  selector: 'c8y-title-outlet',
  templateUrl: './title-outlet.component.html'
})
export class TitleOutletComponent {
  title = undefined;

  constructor(
    private headerService: HeaderService,
    private element: ElementRef,
    private zone: NgZone
  ) {
    const title$ = this.headerService.map(state => state.title);
    title$.subscribe(title => (this.title = title));
    this.zone.runOutsideAngular(() => {
      title$
        .pipe(
          debounceTime(100),
          map(() => this.element.nativeElement.querySelector('h1')),
          filter(el => el !== null),
          map(el => el.innerText),
          filter(Boolean)
        )
        .subscribe((title: string) => headerService.changePageTitle(title));
    });
  }
}
