import { CdkHeaderCell } from '@angular/cdk/table';
import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ComponentFactory,
  ComponentFactoryResolver,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  QueryList,
  ViewChild,
  ViewChildren,
  ViewContainerRef
} from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { IResultList, Paging } from '@c8y/client';
import { flow, get, parseInt as _parseInt, union, without } from 'lodash-es';
import { combineLatest, Subject } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  first,
  mergeMap,
  take,
  takeUntil,
  tap
} from 'rxjs/operators';
import { LoadMoreComponent } from '../common/load-more.component';
import { LoadMoreMode } from '../common/public-api';
import { gettext } from '../i18n/gettext';
import { CellRendererSpec } from './column/cell-renderer';
import { ColumnDirective } from './column/column.directive';
import { FilteringFormRendererSpec, FilteringModifier } from './column/filtering-form-renderer';
import {
  ActionControl,
  BuiltInActionType,
  BulkActionControl,
  Column,
  ColumnDataType,
  DataGrid,
  DataSourceStats,
  DisplayOptions,
  FilteringActionType,
  minColumnGridTrackSize,
  Pagination,
  ratiosByColumnTypes,
  Row,
  ServerSideDataCallback,
  SortOrder,
  SpecialColumnName
} from './data-grid.model';
import { GridDataSource } from './grid-data-source';

@Component({
  selector: 'c8y-data-grid',
  templateUrl: './data-grid.component.html'
})
export class DataGridComponent implements DataGrid, OnChanges, AfterViewInit {
  @Input() title: string = gettext('Items');
  @Input() loadMoreItemsLabel: string = gettext('Load more items');
  @Input() loadingItemsLabel: string = gettext('Loading itemsâ€¦');
  @Input() noMatchingItemsLabel: string = gettext('No matching items.');

  columns: Column[];
  @Input('columns')
  set _columns(columns) {
    this.columns = columns || [];
  }

  dataSource = new GridDataSource();
  rows: Row[];
  @Input('rows')
  set _rows(rows) {
    this.rows = rows || [];
  }

  filteringLabelsParams = {
    filteredItemsCount: 0,
    allItemsCount: 0
  };

  pagination: Pagination;
  paginationLabelParams = {
    pageFirstItemIdx: 0,
    pageLastItemIdx: 0,
    itemsTotal: 0
  };
  readonly possiblePageSizes: number[] = [10, 15, 20, 30, 50];
  readonly minPossiblePageSize = Math.min(...this.possiblePageSizes);
  @Input('pagination')
  set _pagination(pagination) {
    this.pagination = pagination;
    const pageSize = get(this.pagination, 'pageSize');

    if (
      pageSize &&
      !this.possiblePageSizes.find(possiblePageSize => possiblePageSize === pageSize)
    ) {
      this.pagination = { ...this.pagination, pageSize: 10 };
    }
  }

  infiniteScroll: LoadMoreMode;
  @Input('infiniteScroll')
  set _infiniteScroll(infiniteScroll: LoadMoreMode) {
    this.infiniteScroll = infiniteScroll;
  }

  serverSideDataCallback: ServerSideDataCallback;
  @Input('serverSideDataCallback')
  set _serverSideDataCallback(serverSideDataCallback) {
    this.serverSideDataCallback = serverSideDataCallback;
  }

  selectable = false;
  @Input('selectable')
  set _selectable(selectable) {
    this.selectable = selectable;
  }

  selectionPrimaryKey = 'id';
  @Input('selectionPrimaryKey')
  set _selectionPrimaryKey(selectionPrimaryKey) {
    this.selectionPrimaryKey = selectionPrimaryKey;
  }

  displayOptions: DisplayOptions = {
    striped: true,
    bordered: false,
    gridHeader: true
  };
  @Input('displayOptions')
  set _displayOptions(displayOptions) {
    this.displayOptions = { ...this.displayOptions, ...displayOptions };
  }

  actionControls: ActionControl[];
  @Input('actionControls')
  set _actionControls(actionControls) {
    this.actionControls = actionControls || [];
  }

  bulkActionControls: BulkActionControl[];
  @Input('bulkActionControls')
  set _bulkActionControls(bulkActionControls) {
    this.bulkActionControls = bulkActionControls || [];
  }

  @Output() rowMouseOver = new EventEmitter<object>();
  @Output() rowMouseLeave = new EventEmitter<object>();
  @Output() rowClick = new EventEmitter<object>();

  @Output() itemsSelect = new EventEmitter<string[]>();

  @ContentChildren(ColumnDirective) columnRenderers: QueryList<ColumnDirective>;

  @ViewChild('scroll', { static: true }) scrollContainer: ElementRef;

  @ViewChild('infiniteScrollContainer', { static: false, read: ViewContainerRef })
  infiniteScrollContainer: ViewContainerRef;

  loadMoreComponent: LoadMoreComponent;

  columnNames = [];

  styles = {
    tableCursor: 'auto',
    gridTemplateColumns: undefined,
    gridInfiniteScrollColumn: undefined
  };

  searchText$ = new EventEmitter<string>();
  searchText = '';
  filteringApplied = false;
  totalPagesCount = Infinity;

  selectedItemIds: string[] = [];
  currentPageSelectionState = {
    allSelected: false,
    allDeselected: true
  };

  builtInActionType = {
    Edit: BuiltInActionType.Edit,
    Delete: BuiltInActionType.Delete,
    Export: BuiltInActionType.Export
  };

  resizeHandleMouseDown$ = new EventEmitter<any>();
  resizeHandleContainerMouseMove$ = new EventEmitter<any>();
  resizeHandleContainerMouseUp$ = new EventEmitter<any>();
  private headerBeingResized: {
    columnName: string;
    el: HTMLElement;
  };
  @ViewChildren(CdkHeaderCell, { read: ElementRef }) private thRefs: QueryList<ElementRef>;
  private thEls;

  private recreateLoadMoreComponent: boolean;

  private unsubscribe$ = new Subject<any>();

  constructor(
    private sanitizer: DomSanitizer,
    private hostRef: ElementRef,
    private cd: ChangeDetectorRef,
    private cfr: ComponentFactoryResolver
  ) {}

  ngOnInit() {
    this.searchText$
      .pipe(
        takeUntil(this.unsubscribe$),
        debounceTime(300),
        distinctUntilChanged(),
        tap(searchText => {
          this.searchText = searchText;
        })
      )
      .subscribe(() => {
        this.reload();
      });

    if (this.selectable) {
      combineLatest(this.dataSource.data$, this.itemsSelect.asObservable())
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(([data]) => {
          const currentPageEmpty = data.length === 0;

          this.currentPageSelectionState = {
            allSelected: currentPageEmpty ? false : data.every(item => this.isItemSelected(item)),
            allDeselected: currentPageEmpty ? true : data.every(item => !this.isItemSelected(item))
          };
        });
    }
    this.dataSource.stats$.pipe(takeUntil(this.unsubscribe$)).subscribe(stats => {
      this.createLoadMoreComponent(stats);
      this.updateFilteringLabelsParams(stats);
      this.updatePaginationLabelParams(stats);
    });
  }

  ngOnChanges() {
    this.updateColumns();
    this.loadData();
  }

  ngAfterViewInit() {
    this.updateGridColumnsSize();
    this.thEls = this.thRefs.toArray().map(({ nativeElement }) => nativeElement);
    this.setupResizeHandle();
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  trackByName(index, item) {
    return item.name;
  }

  resolveCellValue(row, path) {
    return flow([
      x => this.dataSource.resolveValue(x, path),
      this.dataSource.resolveFunction,
      this.dataSource.normalizeNil
    ])(row);
  }

  changeSortOrder(columnName) {
    const column = this.columns.find(({ name }) => name === columnName);

    if (column) {
      const { sortOrder } = column;

      if (!sortOrder) {
        this.updateSorting([columnName], 'asc');
      } else if (sortOrder === 'asc') {
        this.updateSorting([columnName], 'desc');
      } else {
        this.updateSorting([columnName], '');
      }
    }
  }

  updateSorting(columnNames: string[], sortOrder: SortOrder) {
    this.columns = this.columns.map(
      (column): Column => {
        if (columnNames.includes(column.name)) {
          return { ...column, sortOrder };
        }

        return column;
      }
    );

    this.reload();
  }

  applyFilter(columnName, dropdown, filteringModifier) {
    this.updateFiltering([columnName], {
      type: FilteringActionType.ApplyFilter,
      payload: { filteringModifier }
    });
    dropdown.hide();
  }

  resetFilter(columnName, dropdown) {
    this.updateFiltering([columnName], { type: FilteringActionType.ResetFilter });
    dropdown.hide();
  }

  clearFilters() {
    this.updateFiltering(this.columns.map(({ name }) => name), {
      type: FilteringActionType.ResetFilter
    });
  }

  updateFiltering(
    columnNames: string[],
    action: {
      type: FilteringActionType;
      payload?: { filteringModifier: FilteringModifier };
    }
  ) {
    this.columns = this.columns.map(column => {
      if (columnNames.includes(column.name)) {
        return {
          ...column,
          ...(action.type === FilteringActionType.ApplyFilter
            ? action.payload.filteringModifier
            : { filterPredicate: undefined, externalFilterQuery: undefined })
        };
      }

      return column;
    });

    this.updateFilteringApplied();
    this.reload();
  }

  updateFilteringApplied() {
    this.filteringApplied = this.columns.some(this.isColumnFilteringApplied);
  }

  isColumnFilteringApplied(column: Column): boolean {
    const { filterable, filterPredicate, externalFilterQuery } = column;

    return !!(filterable && (filterPredicate || externalFilterQuery));
  }

  updatePagination({ itemsPerPage, page }) {
    this.pagination = { ...this.pagination, pageSize: itemsPerPage, currentPage: page };
    this.loadData();
  }

  reload() {
    this.pagination = { ...this.pagination, currentPage: 1 };
    this.recreateLoadMoreComponent = true;
    this.loadData(true);
    this.scrollToTop();
  }

  loadNextPage(): Promise<IResultList<object>> {
    this.pagination = { ...this.pagination, currentPage: this.pagination.nextPage };

    this.loadData();

    return this.dataSource.resultList$
      .pipe(take(1)) // in order for `toPromise` to work, the observable needs to complete
      .toPromise()
      .then(result => {
        return {
          ...result,
          paging: {
            ...result.paging,
            next: this.loadNextPage.bind(this)
          }
        } as IResultList<object>;
      });
  }

  getCellRendererSpec({ value, row, columnName }): CellRendererSpec {
    return this._getCellRendererSpec({ type: 'CELL', value, row, columnName });
  }

  getHeaderCellRendererSpec({ value, columnName }): CellRendererSpec {
    return this._getCellRendererSpec({ type: 'HEADER', value, row: undefined, columnName });
  }

  getFilteringFormRendererSpec({ column, dropdown }): FilteringFormRendererSpec {
    return {
      renderer:
        get(this.getColumnRenderer(column), 'filteringFormRendererDef.template') ||
        column.filteringFormRendererComponent,
      context: {
        property: column,
        applyFilter: this.applyFilter.bind(this, column.name, dropdown),
        resetFilter: this.resetFilter.bind(this, column.name, dropdown)
      }
    };
  }

  setAllItemsSelected(selected) {
    this.dataSource.selection$
      .pipe(first())
      .subscribe(({ filteredDataIds }) => this.setItemsSelected(filteredDataIds, selected));
  }

  setAllItemsInCurrentPageSelected(selected) {
    this.dataSource.data$.pipe(first()).subscribe(data => this.setItemsSelected(data, selected));
  }

  setItemsSelected(items: any[], selected) {
    const itemIds = items.map((item: object | string) =>
      typeof item === 'object' ? item[this.selectionPrimaryKey] : item
    );

    this.selectedItemIds = selected
      ? union(this.selectedItemIds, itemIds)
      : without(this.selectedItemIds, ...itemIds);

    this.itemsSelect.emit(this.selectedItemIds);
  }

  isItemSelected(item) {
    return this.selectedItemIds.includes(item[this.selectionPrimaryKey]);
  }

  onColumnDrop({ previousIndex, currentIndex }) {
    const differentIndex = previousIndex !== currentIndex;

    if (differentIndex) {
      const column = this.columns.splice(previousIndex, 1);
      this.columns.splice(currentIndex, 0, column[0]);
    }

    this.updateColumnNames();
    this.updateGridColumnsSize();
  }

  updateGridColumnsSize() {
    this.styles = {
      ...this.styles,
      gridTemplateColumns: this.sanitizer.bypassSecurityTrustStyle(
        this.columns
          .filter(column => column.visible)
          .map(({ gridTrackSize }) => gridTrackSize)
          .join(' ')
      ),
      gridInfiniteScrollColumn: this.sanitizer.bypassSecurityTrustStyle(
        `1 / span ${this.columns.length}`
      )
    };
  }

  private updateColumns() {
    const specialColumn = {
      gridTrackSize: '42px',
      sortable: false,
      positionFixed: true
    };

    this.columns = [
      this.selectable ? { ...specialColumn, name: SpecialColumnName.Checkbox } : undefined,
      ...this.columns,
      this.actionControls.length > 0
        ? { ...specialColumn, name: SpecialColumnName.Actions }
        : undefined
    ]
      .filter(Boolean)
      .map(this.withColumnDefaults);

    this.updateColumnNames();
    this.updateGridColumnsSize();
    this.updateFilteringApplied();
  }

  private withColumnDefaults(column): Column {
    const dataType = column.dataType || ColumnDataType.TextShort;
    const { headerCSSClassName, cellCSSClassName } = column;

    return {
      visible: true,
      positionFixed: false,
      sortable: true,
      sortOrder: '',
      filterable: false,
      ...column,
      dataType,
      gridTrackSize:
        column.gridTrackSize ||
        `minmax(${minColumnGridTrackSize}px, ${ratiosByColumnTypes[dataType]}fr)`,
      headerCSSClassName:
        (typeof headerCSSClassName === 'string'
          ? headerCSSClassName.split(' ')
          : headerCSSClassName) || [],
      cellCSSClassName:
        (typeof cellCSSClassName === 'string' ? cellCSSClassName.split(' ') : cellCSSClassName) ||
        []
    };
  }

  private updateColumnNames() {
    this.columnNames = this.columns.map(({ name }) => name);
  }

  private setupResizeHandle() {
    const resizeHandleDrag$ = this.resizeHandleMouseDown$.pipe(
      takeUntil(this.unsubscribe$),
      tap(() => this.clearMouseHighlights()),
      mergeMap(({ event, targetColumnName }) => {
        this.columns = this.columns.map(column => {
          if (column.name === targetColumnName) {
            return {
              ...column,
              headerCSSClassName: union(column.headerCSSClassName, ['header--being-resized'])
            };
          }

          return column;
        });

        this.headerBeingResized = {
          columnName: targetColumnName,
          el: event.target.parentNode
        };

        this.styles = {
          ...this.styles,
          tableCursor: 'col-resize'
        };

        return this.resizeHandleContainerMouseMove$.pipe(
          tap(() => this.clearMouseHighlights()),
          takeUntil(this.resizeHandleContainerMouseUp$)
        );
      })
    );

    resizeHandleDrag$.subscribe(event => {
      requestAnimationFrame(() => {
        this.columns = this.columns.map((column, i) => {
          if (column.name === this.headerBeingResized.columnName) {
            const wrapper = this.hostRef.nativeElement;

            // Read wrapper's offset left relative to the document.
            const horizontalOffset = wrapper.getBoundingClientRect().left;

            // Adjust with the wrapper horizontal scroll position.
            const horizontalScrollOffset = wrapper.scrollLeft - horizontalOffset;

            // Calculate the desired width.
            const width =
              horizontalScrollOffset + event.clientX - (this.headerBeingResized.el.offsetLeft || 0);

            return {
              ...column,
              // Update the column object with the new size value, enforce our minimum size.
              gridTrackSize: `${Math.max(minColumnGridTrackSize, width)}px`
            };
          }

          // For the other headers which don't have a set width, fix it to their computed width.
          if (column.gridTrackSize.startsWith('minmax')) {
            return {
              ...column,
              // isn't fixed yet (it would be a px value)
              gridTrackSize: `${_parseInt(this.thEls[i].clientWidth)}px`
            };
          }

          return column;
        });

        /*
         * Update the column sizes.
         * Note: grid-template-columns sets the width for all columns in one value.
         */
        this.updateGridColumnsSize();
      });
    });

    this.resizeHandleContainerMouseUp$.pipe(takeUntil(this.unsubscribe$)).subscribe(() => {
      if (this.headerBeingResized) {
        this.columns = this.columns.map(column => {
          if (column.name === this.headerBeingResized.columnName) {
            return {
              ...column,
              headerCSSClassName: without(column.headerCSSClassName, 'header--being-resized')
            };
          }

          return column;
        });
        this.headerBeingResized = undefined;

        this.styles = {
          ...this.styles,
          tableCursor: 'auto'
        };
      }
    });
  }

  private clearMouseHighlights() {
    if (window.getSelection) {
      window.getSelection().removeAllRanges();
    }
  }

  private _getCellRendererSpec({ type, value, row, columnName }): CellRendererSpec {
    const column = this.columns.find(({ name }) => name === columnName);
    const columnRenderer = this.getColumnRenderer(column);
    const rendererTemplate = get(
      columnRenderer,
      `${type === 'HEADER' ? 'headerCellRendererDef' : 'cellRendererDef'}.template`
    );

    const { cellRendererComponent, headerCellRendererComponent } = column;
    const rendererComponent =
      type === 'HEADER' ? headerCellRendererComponent : cellRendererComponent;

    return {
      renderer: rendererTemplate || rendererComponent,
      context: {
        value,
        item: row,
        property: column
      }
    };
  }

  private getColumnRenderer(column): ColumnDirective {
    return this.columnRenderers.toArray().find(({ name }) => name === column.name);
  }

  private loadData(reload: boolean = false) {
    const {
      rows,
      columns,
      pagination,
      searchText,
      serverSideDataCallback,
      selectable,
      selectionPrimaryKey,
      infiniteScroll
    } = this;

    this.dataSource.loadData({
      rows,
      columns,
      pagination,
      searchText,
      serverSideDataCallback,
      selectable,
      selectionPrimaryKey,
      infiniteScroll,
      reload
    });
  }

  private updateFilteringLabelsParams(stats: DataSourceStats) {
    this.filteringLabelsParams = {
      filteredItemsCount: stats.filteredSize,
      allItemsCount: stats.size
    };
  }

  private updatePaginationLabelParams(stats: DataSourceStats) {
    this.pagination.nextPage = stats.nextPage;
    const pageFirstItemIdx = (stats.currentPage - 1) * stats.firstPageSize + 1;
    this.paginationLabelParams = {
      pageFirstItemIdx,
      pageLastItemIdx: pageFirstItemIdx + (stats.currentPageSize - 1),
      itemsTotal: stats.filteredSize
    };
  }

  private createLoadMoreComponent(stats: DataSourceStats) {
    if (
      this.infiniteScroll &&
      stats &&
      stats.nextPage &&
      (!this.loadMoreComponent || this.recreateLoadMoreComponent)
    ) {
      this.recreateLoadMoreComponent = false;
      this.infiniteScrollContainer.clear();
      const componentFactory: ComponentFactory<any> = this.cfr.resolveComponentFactory(
        LoadMoreComponent
      );
      const componentRef = this.infiniteScrollContainer.createComponent(componentFactory);
      const instance = componentRef.instance as LoadMoreComponent;
      instance.useIntersection = this.infiniteScroll === 'auto' || this.infiniteScroll === 'hidden';
      instance.hidden = this.infiniteScroll === 'hidden';
      instance.paging = {
        nextPage: stats.nextPage,
        next: this.loadNextPage.bind(this)
      } as Paging<any>;
      instance.loadNextLabel = this.loadMoreItemsLabel;
      instance.loadingLabel = this.loadingItemsLabel;
      this.loadMoreComponent = instance;
    } else if (this.loadMoreComponent && !stats.nextPage) {
      this.loadMoreComponent.paging = {
        nextPage: null
      } as Paging<any>;
    }
  }

  private scrollToTop() {
    if (this.infiniteScroll) {
      this.scrollContainer.nativeElement.scrollTop = 0;
    }
  }
}
