import {
  Component,
  Input,
  HostBinding,
  Renderer2,
  ElementRef,
  OnChanges,
  ViewChild,
  OnDestroy,
  AfterContentInit,
  AfterContentChecked} from '@angular/core';
import { debounceTime } from 'rxjs/operators';
import { fromEventPattern, fromEvent, merge, Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { Tab } from './tab.model';

@Component({
  selector: 'c8y-tabs-outlet,c8y-ui-tabs',
  templateUrl: './tabs-outlet.component.html',
})
export class TabsOutletComponent implements OnDestroy, AfterContentInit {
  @Input() tabs: any[] = [];
  @Input() orientation: 'vertical' | 'horizontal' = 'vertical';
  @Input() navigatorOpen = false;
  showLeft: boolean = false;
  showRight: boolean = false;
  @HostBinding('class.navigator-open') get isNavigatorOpen() {
    return this.navigatorOpen;
  }
  @HostBinding('class.page-tabs') get hasTabs() {
    return this.tabs && this.tabs.length;
  }
  @HostBinding('class.page-tabs-horizontal') get isHorizontal() {
    return this.orientation === 'horizontal';
  }
  @HostBinding('class.page-tabs-vertical') get isVertical() {
    return this.orientation !== 'horizontal';
  }
  @ViewChild('container', {read: ElementRef, static: true}) private container: ElementRef;
  @ViewChild('left', {read: ElementRef, static: false}) private leftBtn: ElementRef;
  @ViewChild('right', {read: ElementRef, static: false}) private rightBtn: ElementRef;
  @ViewChild('tabsContainer', {read: ElementRef, static: false}) private tabsContainer: ElementRef;
  private scrollSubscription: Subscription;

  constructor(
    private router: Router,
    private renderer: Renderer2,
    private el: ElementRef
  ) {}

  ngOnDestroy() {
    if (this.scrollSubscription) {
      this.scrollSubscription.unsubscribe();
    }
  }

  ngAfterContentInit() {
    if (this.orientation === 'horizontal') {
      this.scrollSubscribe();
    }

  }

  isActive(tab: Tab) {
    if (tab && tab.path) {
      return this.router.isActive(tab.path, false);
    } else {
      return false;
    }
  }

  navigateTo(path) {
    this.router.navigate([path]);
  }

  scroll(direction: 'right' | 'left' = 'right') {
    const { widthScroll, width, scrollLeft } = this.getDimensions();
    const multiplier = direction === 'left' ? -1 : 1;

    let targetScroll = scrollLeft + width * multiplier;
    if (targetScroll < 0) {
      targetScroll = 0;
    }
    if (targetScroll > widthScroll - width) {
      targetScroll = widthScroll - width;
    }
    // TODO: we should animate this
    this.container.nativeElement.scrollLeft = targetScroll;
  }

  private scrollSubscribe() {
    let unlisten;
    this.scrollSubscription = merge(
      fromEvent(this.container.nativeElement, 'scroll'),
      fromEventPattern(
        (handler: any) => {
          unlisten = this.renderer.listen('window', 'resize', handler);
        },
        () => unlisten()
      )
    ).pipe(
      debounceTime(250)
    ).subscribe(() => this.updateScroll());
  }

  private updateScroll() {
    const { widthScroll, width, scrollLeft } = this.getDimensions();
    this.showLeft = !!scrollLeft;
    this.showRight = widthScroll > width + scrollLeft;
  }

  private getDimensions() {
    const elTabs = this.tabsContainer.nativeElement;
    const el = this.container.nativeElement;
    const widthScroll =   elTabs.scrollWidth;
    const width = el.clientWidth;
    const scrollLeft = el.scrollLeft;
    return { widthScroll, width, scrollLeft };
  }
}
