import { Component, ContentChildren, QueryList } from '@angular/core';
import { Breadcrumb, BreadcrumbItem } from './breadcrumb.model';
import { BreadcrumbService } from './breadcrumb.service';
import { BreadcrumbItemComponent } from './breadcrumb-item.component';

@Component({
  selector: 'c8y-breadcrumb',
  templateUrl: './breadcrumb.component.html'
})
export class BreadcrumbComponent implements Breadcrumb {
  items: BreadcrumbItem[];
  @ContentChildren(BreadcrumbItemComponent) private contentChildItems: QueryList<BreadcrumbItem>;
  private viewInitTimeout;

  constructor(private breadcrumbService: BreadcrumbService) {}

  ngAfterViewInit() {
    this.items = this.contentChildItems.toArray();
    this.viewInitTimeout = setTimeout(() =>
      this.breadcrumbService.add(this as Breadcrumb)
    );
  }

  ngOnDestroy() {
    clearTimeout(this.viewInitTimeout);
    this.breadcrumbService.remove(this as Breadcrumb);
  }
}
