import { Component, EventEmitter, Input, Output } from '@angular/core';
import { PasswordService } from './password.service';

@Component({
  selector: 'c8y-password-strength',
  templateUrl: './password-strength.component.html'
})

export class PasswordStrengthComponent {

  @Input('password')
  set password(password: string) {
    if (typeof password === 'string') {
      this.model = this.passwordStrengthCheckerService.getStrengthColor(password);
      this.strength.emit(this.model);
    }
  }
  @Output()
  strength = new EventEmitter();

  model;

  constructor(private passwordStrengthCheckerService: PasswordService) {
    this.password = '';
  }
}
