import {
  Component,
  Input,
  ViewChild,
  ComponentFactoryResolver,
  ViewContainerRef,
  ComponentRef
} from '@angular/core';
import {
  DynamicComponent,
  DynamicComponentDefinition,
  OnBeforeSave
} from './dynamic-component.model';
import { DynamicComponentService } from './dynamic-component.service';
import { of, isObservable, Observable } from 'rxjs';
import { isUndefined } from 'lodash-es';

/**
 * C8y dynamic component.
 * ## Example:
 *
 * register component in HOOK in module:
 * ```typescript
 *  import { HOOK_COMPONENT } from '@c8y/ngx-components';
 *
 * @NgModule({
 *  ...,
 *  providers: [{
 *      provide: HOOK_COMPONENT,
 *      multi: true,
 *      useValue: [{
 *          id: 'test-component',
 *          label: 'My test component',
 *          description: 'this is test component',
 *          component: TestComponent
 *      }],
 *  ...
 *  }]
 *
 * ```
 * Showing dynamic component:
 * ```html
 * <c8y-dynamic-component [componentId]="'test-component'" [config]="config"></c8y-dynamic-component>
 * ```
 */
@Component({
  selector: 'c8y-dynamic-component',
  templateUrl: './dynamic-component.component.html'
})
export class DynamicComponentComponent {
  /**
   * The ID of the registered component. It needs to be a component that is hooked
   * with the HOOK_COMPONENTS extension hook.
   */
  @Input() componentId: string;
  /**
   * The configuration to pass.
   */
  @Input() config: any;
  /**
   * DynamicComponents can have two modes, an edit (config) and an view (component) mode.
   * By default it is shown in the component mode.
   */
  @Input() mode: 'config' | 'component' = 'component';
  /**
   * Disable this to hide the error that is shown if the component was not found.
   */
  @Input() notFoundError = true;
  /**
   * @ignore
   */
  @ViewChild('host', { read: ViewContainerRef, static: true }) host: ViewContainerRef;
  /**
   * @ignore
   */
  error: any;
  /**
   * @ignore
   */
  expandErrorDetails = false;
  private componentRef: ComponentRef<Component>;

  /**
   * @ignore only DI
   */
  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private dynamicComponentService: DynamicComponentService
  ) {}

  /**
   * Calls the dynamic component life cycle hook. Currently only
   * supporting onBeforeSave, a hook which is called before a config component
   * is saved.
   */
  callLifeCycleHooks() {
    return this.callOnBeforeSaveHook();
  }

  /**
   * @ignore
   */
  ngOnChanges(): void {
    this.dynamicComponentService
      .getById$(this.componentId)
      .subscribe(cmp => this.loadComponent(cmp));
  }

  private loadComponent(dynamicComponent: DynamicComponentDefinition) {
    try {
      this.error = undefined;
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(
        this.mode === 'component' ? dynamicComponent.component : dynamicComponent.configComponent
      );
      this.host.clear();
      this.componentRef = this.host.createComponent(componentFactory);
      (this.componentRef.instance as DynamicComponent).config = this.config;
    } catch (ex) {
      this.error = ex;
    }
  }

  private callOnBeforeSaveHook() {
    if (!this.componentRef) {
      return of(true);
    }
    const hook = (this.componentRef.instance as OnBeforeSave).onBeforeSave;
    if (hook) {
      const result = hook(this.config);
      if (isUndefined(result)) {
        return of(true);
      }
      return isObservable(result) ? result : (of(result) as Observable<boolean>);
    }
    return of(true);
  }
}
